import {Reducer} from "redux";
import {REQUEST_ERROR, REQUEST_INIT, REQUEST_SUCCESS} from "../../middlewares/request/requestActions";
import {
  DASHBOARDS_DASHBOARD_FETCH,
  DASHBOARDS_DASHBOARDS_CLEAR,
  DASHBOARDS_DATASET_FETCH,
  DASHBOARDS_DATASET_FETCH_ENABLE_STATUS_SET,
  DASHBOARDS_MAP_GEOMETRIES_FETCH
} from "./dashboardActions";
import _ from "lodash"
import {DASHBOARD_ELEM_FILTER_DIMENSION_KEY, getViewIdxFromRowAndCol} from "../../utils/dashboards";

export const DASHBOARD_VIEW_STATE_APPLY_FILTERS = "DASHBOARD_VIEW_STATE_APPLY_FILTERS"
export const DASHBOARD_VIEW_STATE_ERROR = "DASHBOARD_VIEW_STATE_ERROR"
export const DASHBOARD_VIEW_STATE_FETCHING = "DASHBOARD_VIEW_STATE_FETCHING"

type DashboardState = {
  dashboards: any | null,
  isFetchDatasetDisabled: boolean,
  jsonStats: any | null,
  maps: any | null
}

const initialState = {
  dashboards: null,
  isFetchDatasetDisabled: true,
  jsonStats: null,
  maps: null
};

const dashboardReducer: Reducer<DashboardState> = (
  state = initialState,
  action
) => {
  switch (action.type) {
    case DASHBOARDS_DASHBOARDS_CLEAR: {
      return initialState
    }
    case DASHBOARDS_DATASET_FETCH_ENABLE_STATUS_SET: {
      return {
        ...state,
        isFetchDatasetDisabled: action.payload.disabled
      }
    }
    case REQUEST_INIT: {
      switch (action.payload.label) {
        case DASHBOARDS_DATASET_FETCH: {

          let currentDashboardJsonStats = {};
          state.dashboards[action.payload.extra.dashboardId].dashboardConfig.forEach((row: any, rowIdx: number) => {
            row.forEach((col: any, colIdx: number) => {
              const viewIdx = getViewIdxFromRowAndCol(rowIdx, colIdx);

              if (!col[DASHBOARD_ELEM_FILTER_DIMENSION_KEY] || col[DASHBOARD_ELEM_FILTER_DIMENSION_KEY].length === 0) {
                currentDashboardJsonStats = {
                  ...currentDashboardJsonStats,
                  [viewIdx]: (state.jsonStats?.[action.payload.extra.dashboardId]?.[viewIdx] || DASHBOARD_VIEW_STATE_FETCHING)
                }
              } else {
                currentDashboardJsonStats = {
                  ...currentDashboardJsonStats,
                  [viewIdx]: DASHBOARD_VIEW_STATE_FETCHING
                }
              }
            });
          });

          return {
            ...state,
            jsonStats: {
              ...state.jsonStats,
              [action.payload.extra.dashboardId]: currentDashboardJsonStats
            }
          }
        }
        case DASHBOARDS_MAP_GEOMETRIES_FETCH: {
          return {
            ...state,
            maps: {
              ...state.maps,
              [action.payload.extra.dashboardId]: null
            }
          }
        }
        default:
          return state
      }
    }
    case REQUEST_SUCCESS: {
      switch (action.payload.label) {
        case DASHBOARDS_DASHBOARD_FETCH: {
          const views = action.payload.response.views;
          for (let key in views) {
            if (views.hasOwnProperty(key)) {
              views[key] = {
                ...views[key],
                datasetId: views[key].datasetId ? views[key].datasetId.split("+").join(",") : undefined
              }
            }
          }

          return {
            ...state,
            dashboards: {
              ...state.dashboards,
              [action.payload.extra.dashboardId]: {
                ...action.payload.response,
                dashboardConfig: action.payload.response?.dashboardConfig
                  ? JSON.parse(action.payload.response.dashboardConfig)
                  : {},
                filterLevels: action.payload.response?.filterLevels
                  ? JSON.parse(action.payload.response.filterLevels)
                  : {},
                views: views
              }
            }
          }
        }
        case DASHBOARDS_DATASET_FETCH: {
          let jsonStats = _.cloneDeep(state.jsonStats);
          const jsonStat = (action.payload.response?.id || []).length > 0
            ? action.payload.response
            : "";

          (action.payload.extra.requestIds || []).forEach((id: string) => {
            jsonStats = {
              ...jsonStats,
              [action.payload.extra.dashboardId]: {
                ...jsonStats[action.payload.extra.dashboardId],
                [id]: jsonStat
              }
            }
          });

          return {
            ...state,
            jsonStats: jsonStats
          }
        }
        case DASHBOARDS_MAP_GEOMETRIES_FETCH: {
          const detailLevels = action.payload.response
            .map((val: any, idx: number) => val.nutsLevel)
            .reduce((acc: any, val: any) => {
              if (!acc[val]) {
                const label = "Nuts level " + val;
                acc[val] = {level: val, counter: 0, label: label};
              }
              acc[val].counter++;
              return acc;
            }, []);

          return {
            ...state,
            maps: {
              ...state.maps,
              [action.payload.extra.dashboardId]: {
                ...state.maps[action.payload.extra.dashboardId],
                [action.payload.extra.requestId]: {
                  geometries: action.payload.response,
                  geometryDetailLevels: detailLevels
                }
              }
            }
          }
        }
        default:
          return state
      }
    }
    case REQUEST_ERROR: {
      switch (action.payload.label) {
        case DASHBOARDS_DATASET_FETCH: {
          let jsonStats = _.cloneDeep(state.jsonStats);

          (action.payload.extra.requestIds || []).forEach((id: string) => {
            jsonStats = {
              ...jsonStats,
              [action.payload.extra.dashboardId]: {
                ...jsonStats[action.payload.extra.dashboardId],
                [id]: DASHBOARD_VIEW_STATE_ERROR
              }
            }
          });

          return {
            ...state,
            jsonStats: jsonStats
          }
        }
        default:
          return state
      }
    }
    default:
      return state
  }
};

export default dashboardReducer;