export const goToDatasets = (nodeCode, categoryPath) =>
  window.open(`./#/${nodeCode.toLowerCase()}/${categoryPath.join("/")}`, '_self');

export const goToData = (nodeCode, categoryPath, datasetId, viewId, a11y) =>
  window.open(
    `./#/${nodeCode.toLowerCase()}/${categoryPath.length > 0 ? categoryPath.join("/") + "/" : ""}` +
    `${datasetId}${(viewId || a11y) ? '?' : ''}${viewId ? `view=${viewId}` : ""}${a11y ? "accessible=true" : ""}`,
    '_self'
  );

// filters = c1/c2,c3/c4
export const goToDatasetsSearch = (nodeCode, query, filters) => {
  window.open(
    `./#/${nodeCode.toLowerCase()}/search?q=${encodeURIComponent(query)}` +
    (filters && filters.length > 0 ? `&c=${encodeURIComponent(filters.map(f => encodeURIComponent(f.join("/"))).join("//"))}` : ''),
    '_self'
  );
};

export const goToNode = nodeCode => window.open(`./#/${nodeCode}`, '_self');
export const goToHome = () => window.open('./#/', '_self');
export const goToSdg = () => window.open('./#/sdg', '_self');

export const goToHubDashboards = () => window.open('./#/dashboards', '_self');
export const goToNodeDashboards = nodeCode => window.open(`./#/${nodeCode.toLowerCase()}/dashboards`, '_self');
export const goToDashboard = dashboardId => window.open(`./#/dashboards/${dashboardId}`, '_self');