import React, {Fragment, useState} from 'react';
import {withStyles} from "@material-ui/core";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";
import {getTextWidth} from "../../utils/style";
import InfiniteScrollTable from "../infinite-scroll-table";
import {
  LABEL_FORMAT_SELECTOR_LABEL_FORMAT_BOTH,
  LABEL_FORMAT_SELECTOR_LABEL_FORMAT_ID,
  LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME
} from "../label-format-selector";
import {getDimensionAttributeMap, getDimensionFiltersValue} from "../../utils/jsonStat";
import {useTranslation} from "react-i18next";

const $ = window.jQuery;

const styles = theme => ({
  root: {},
  filter: {
    display: "inline-block",
    verticalAlign: "bottom",
    minWidth: 120,
    marginRight: 24,
    marginBottom: 8
  },
  dialogContent: {
    padding: "8px !important"
  },
  item: {
    fontSize: 14
  },
  itemDisabled: {
    pointerEvents: "none",
    '& > svg': {
      visibility: "hidden"
    }
  },
  dimensionTitle: {
    marginBottom: 4,
    fontSize: 13,
    color: theme.palette.primary.main
  }
});

const getFormattedName = (id, name, labelFormat) => {
  switch (labelFormat) {
    case LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME:
      return name;
    case LABEL_FORMAT_SELECTOR_LABEL_FORMAT_ID:
      return id;
    case LABEL_FORMAT_SELECTOR_LABEL_FORMAT_BOTH:
      return `[${id}] ${name}`;
    default:
      return name;
  }
};

function DatasetFilters(props) {
  const {
    classes,
    jsonStat,
    hiddenAttributes,
    layout,
    labelFormat,
    multiSelectDimensions,
    multiSelectDimensionsValues,
    onSelect
  } = props;

  const {
    filtersValue
  } = layout;

  const {t} = useTranslation();

  const [filters] = useState((multiSelectDimensions || []).filter(el => el !== null && el !== undefined).concat(layout.filters || []));

  const [visibleId, setVisibleId] = useState(null);

  const [checkedKeys, setCheckedKeys] = useState(null);

  const handleFilterClose = () => {
    setVisibleId(null);
    setCheckedKeys(null);
  };

  const isMultiSelectDim = dim => (multiSelectDimensions && multiSelectDimensions.length > 0)
    ? multiSelectDimensions.includes(dim)
    : false;

  const dimensionAttributesMap = getDimensionAttributeMap(jsonStat, hiddenAttributes);

  let data = [];
  if (visibleId !== null) {
    data = getDimensionFiltersValue(jsonStat, filters[visibleId], layout.filters, filtersValue);
    if (checkedKeys === null && multiSelectDimensions !== undefined && multiSelectDimensionsValues !== undefined) {
      setCheckedKeys(multiSelectDimensionsValues[filters[visibleId]]);
    }
  }

  return (
    <Fragment>
      <div className={classes.root}>
        {(() => {
          const getTextWidthEl = $('<span>').css({
            visibility: 'hidden',
            position: 'absolute',
            fontSize: 16
          }).appendTo('body').get(0);

          const select = [];
          filters.forEach((filter, idx) => {

            if ((multiSelectDimensions && multiSelectDimensions.includes(filter)) || jsonStat.size[jsonStat.id.indexOf(filter)] !== 1) {

              const dimLabel = (jsonStat.dimension[filter].label || filter);
              const minWidth = getTextWidth(dimLabel, getTextWidthEl) + 16;

              select.push(
                <div key={idx} className={classes.filter}>
                  {isMultiSelectDim(filter) && (
                    <InputLabel className={classes.dimensionTitle}>
                      {multiSelectDimensions[0] === filter
                        ? t("components.datasetFilters.primaryDim")
                        : t("components.datasetFilters.secondaryDim")
                      }
                    </InputLabel>
                  )}
                  <FormControl style={{display: "inline-block", verticalAlign: "bottom"}}>
                    <InputLabel>{dimLabel}</InputLabel>
                    <Select
                      open={false}
                      style={{minWidth: minWidth}}
                      value={isMultiSelectDim(filter)
                        ? multiSelectDimensionsValues[filter]
                        : filtersValue[filter]
                      }
                      multiple={isMultiSelectDim(filter)}
                      renderValue={value => isMultiSelectDim(filter)
                        ? jsonStat.size[jsonStat.id.indexOf(filter)] !== 1
                          ? value.length === 1
                            ? t("components.datasetFilters.selectedCount", {selected: value.length})
                            : t("components.datasetFilters.selectedCount_plural", {selected: value.length})
                          : getFormattedName(value[0], jsonStat.dimension[filter].category.label[value[0]], labelFormat)
                        : getFormattedName(value, jsonStat.dimension[filter].category.label[value], labelFormat)
                      }
                      className={`${classes.item} ${jsonStat.size[jsonStat.id.indexOf(filter)] === 1 ? classes.itemDisabled : ""}`}
                      onOpen={() => jsonStat.size[jsonStat.id.indexOf(filter)] === 1
                        ? null
                        : setVisibleId(idx)
                      }
                    >
                      {jsonStat.dimension[filter].category.index.map((el, idx) => (
                        <MenuItem key={idx + el} value={el}>{el}</MenuItem>
                      ))}
                    </Select>
                  </FormControl>
                  {dimensionAttributesMap && dimensionAttributesMap[filter][filtersValue[filter]] && ( // TODO: attributi per dimensioni multiselezionabili
                    <div style={{display: "inline-block", verticalAlign: "bottom", marginLeft: 8, marginBottom: 4}}>
                      <Tooltip
                        title={
                          <div>
                            {dimensionAttributesMap[filter][filtersValue[filter]].attributes.map(({id, label, valueId, valueLabel}, idx) => (
                              <div
                                key={idx}>{`${label || id}: ${valueLabel || valueId}${valueLabel !== valueId ? ` [${valueId}]` : ''}`}</div>
                            ))}
                          </div>
                        }
                        placement="top"
                      >
                        <div>
                          {`[${dimensionAttributesMap[filter][filtersValue[filter]].ids.join(", ")}]`}
                        </div>
                      </Tooltip>
                    </div>
                  )}
                </div>
              )
            }
          });

          $(getTextWidthEl).remove();

          return select
        })()}
      </div>

      <Dialog
        open={visibleId !== null}
        onClose={handleFilterClose}
        fullWidth
        maxWidth="md"
      >
        <DialogContent className={classes.dialogContent}>
          {!isMultiSelectDim(filters[visibleId])
            ? (
              <InfiniteScrollTable
                data={data}
                getRowKey={({id}) => id}
                showHeader={false}
                columns={[
                  {
                    title: "",
                    dataIndex: 'label',
                    render: (_, {id, label}) => getFormattedName(id, label, labelFormat),
                    minWidth: 100
                  }
                ]}
                onRowClick={rowData => {
                  onSelect(filters[visibleId], rowData.id);
                  handleFilterClose();
                }}
                getRowStyle={rowData => ({
                  background: rowData.id === filtersValue[filters[visibleId]]
                    ? "#fff9e5"
                    : undefined
                })}
              />
            )
            : (
              <InfiniteScrollTable
                data={data}
                getRowKey={({id}) => id}
                columns={[
                  {
                    title: t("components.datasetFilters.table.columns.label.title"),
                    dataIndex: 'label',
                    render: (_, {id, label}) => getFormattedName(id, label, labelFormat),
                    minWidth: 100,
                    noFilter: true
                  }
                ]}
                rowSelection={{
                  selectedRowKeys: checkedKeys,
                  onChange: setCheckedKeys
                }}
              />
            )
          }

        </DialogContent>
        <DialogActions>
          <Button onClick={handleFilterClose} color="primary">
            {t("commons.confirm.cancel")}
          </Button>
          {isMultiSelectDim(filters[visibleId]) && (
            <Button
              onClick={() => {
                onSelect(filters[visibleId], checkedKeys);
                handleFilterClose();
              }}
              color="primary"
            >
              {t("commons.confirm.confirm")}
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </Fragment>
  )
}

export default withStyles(styles)(DatasetFilters);