import React from 'react';
import Dialog from "@material-ui/core/Dialog";
import {DialogActions} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import {useTranslation} from "react-i18next";

const UserLogoutDialog = ({open, onSubmit, onHide, email}) => {

  const {t} = useTranslation();

  return (
    <Dialog
      open={open}
      disableEscapeKeyDown
      disableBackdropClick
      maxWidth="xs"
      fullWidth
      onClose={onHide}
    >
      <DialogTitle>
        {t("components.userLogoutDialog.title")}
      </DialogTitle>
      <DialogContent>
        {t("components.userLogoutDialog.loggedInAs")} <b>{email}</b>
      </DialogContent>
      <DialogActions>
        <Button onClick={onHide}>
          {t("commons.confirm.cancel")}
        </Button>
        <Button onClick={onSubmit}>
          {t("components.userLogoutDialog.logout")}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default UserLogoutDialog;