import React, {Fragment, useEffect, useState} from 'react';
import {useTranslation} from "react-i18next";
import withStyles from "@material-ui/core/styles/withStyles";
import IconButton from "@material-ui/core/IconButton";
import InfoIcon from '@material-ui/icons/Info';
import ShareIcon from '@material-ui/icons/Share';
import GetAppIcon from '@material-ui/icons/GetApp';
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import {goToNode} from "../../links";
import HomeIcon from "@material-ui/icons/Home";
import AttachFileIcon from '@material-ui/icons/AttachFile';
import AccessTimeIcon from '@material-ui/icons/AccessTime';
import Divider from "@material-ui/core/Divider";
import {GENERATING_HTML_TIME_KEY, OBSERVATION_COUNT_KEY, SERVER_TIMINGS_KEY} from "../../state/dataset/datasetReducer";
import ButtonSelect from "../button-select";
import SaveIcon from '@material-ui/icons/Save';
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import CustomEmpty from "../custom-empty";
import TextField from "@material-ui/core/TextField";
import FormControl from "@material-ui/core/FormControl";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Checkbox from "@material-ui/core/Checkbox";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import JsonStatTable from "../table";
import AttributeList from "../attribute-list";
import {
  getDimensionAttributeMap,
  getDimensionLabelFromJsonStat,
  getDimensionValueLabelFromJsonStat
} from "../../utils/jsonStat";
import {DECIMAL_PLACES_DEFAULT, DECIMAL_SEPARATOR_DEFAULT, EMPTY_CHAR_DEFAULT} from "../../utils/formatters";
import {
  canSaveAsView,
  canSaveTemplate,
  canShare,
  canViewTemplateOrAnnotationIcon,
  canViewTimesLog
} from "../../utils/user";
import {connect} from "react-redux";
import {compose} from "redux";
import I18nTextField from "../i18n-text-field";
import {localizeI18nObj, validateI18nObj} from "../../utils/i18n";
import {
  getDownloadFormatExtensionFromFormat,
  getDownloadFormatLabelFromFormat,
  isDownloadFormatValid
} from "../../utils/download";

const styles = theme => ({
  root: {
    width: "100%",
    height: "100%",
    padding: "4px 16px 0"
  },
  headerTitle: {
    marginRight: 8
  },
  headerActions: {},
  title: {
    fontSize: 20,
    fontWeight: 500
  },
  titleAction: {},
  subtitle: {
    padding: "0 12px 8px 48px"
  },
  card: {
    padding: theme.spacing(2)
  },
  viewer: {
    width: "100%",
    height: "calc(100% - 16px - 64px)",
    marginTop: 16
  },
  field: {},
  tabContent: {
    overflowY: "auto",
    overflowX: "hidden",
    height: "calc(100% - 48px)",
    paddingTop: 16
  },
  attributeIcon: {
    width: 24,
    height: 24,
    fontSize: 23,
    fontWeight: "bold",
    fontFamily: "Do Hyeon"
  },
  icon: {
    width: 48,
    height: 48,
    padding: 12,
    color: "rgba(0, 0, 0, 0.54)"
  }
});

const mapStateToProps = state => ({
  user: state.user,
  defaultLanguage: state.app.language,
  languages: state.app.languages
});

function DataViewerHeader(props) {

  const {
    classes,
    user,
    defaultLanguage,
    languages,
    nodeId,
    nodeCode,
    nodeDownloadFormats,
    datasetId,
    datasetTitle,
    hasTemplateLayout,
    hasAnnotationLayout,
    notes,
    attachedFiles,
    jsonStat,
    hiddenAttributes,
    labelFormat,
    tableLayout,
    chartLayout,
    mapLayout,
    criteria,
    decimalSeparator: initialDecimalSeparator,
    decimalPlaces,
    emptyChar,
    viewers,
    viewerIdx,
    onViewTemplateSubmit,
    onDownloadSubmit,
    timings
  } = props;

  const {t} = useTranslation();

  const [initialTemplate] = useState({
    datasetId: "",
    type: "",
    title: {},
    defaultView: "",
    criteria: {},
    layouts: {
      tableDefaultLayout: "",
      tableLayout: {},
      tableEmptyChar: "",
      mapLayout: {},
      chartLayout: {}
    },
    decimalNumber: "",
    decimalSeparator: {},
    enableCriteria: true,
    enableLayout: true
  });

  const [isViewVisible, setViewVisibility] = useState(false);

  const [isTemplateVisible, setTemplateVisibility] = useState(false);
  const [tabId, setTabId] = useState(0);

  const [template, setTemplate] = useState(initialTemplate);

  const [isAttributesVisible, setAttributesVisibility] = useState(false);

  const [isNotesVisible, setNotesVisibility] = useState(false);

  const [downloadFormats, setDownloadFormats] = useState([]);

  const [isTimingsVisible, setTimingsVisibility] = useState(false);

  useEffect(() => {
    const actionsWidth = document.getElementById("dataset-viewer-header__actions")
      ? document.getElementById("dataset-viewer-header__actions").offsetWidth
      : 0;
    document.getElementById("dataset-viewer-header__title").setAttribute("style", `width: calc(100% - ${actionsWidth}px - 8px)`);
  });

  useEffect(() => {
    const newDownloadFormats = [];
    (nodeDownloadFormats || []).forEach(format => {
      if (isDownloadFormatValid(format)) {
        newDownloadFormats.push({
          label: getDownloadFormatLabelFromFormat(format, t),
          format: format,
          extension: getDownloadFormatExtensionFromFormat(format, t)
        })
      }
    });
    setDownloadFormats(newDownloadFormats);
  }, [nodeDownloadFormats, t]);

  const handleViewOpen = defaultLanguage => {
    setViewVisibility(true);
    setTemplate({
      datasetId: datasetId,
      title: {
        [defaultLanguage]: `${viewers[viewerIdx].title} - ${datasetTitle}`
      },
      defaultView: viewers[viewerIdx].type,
      criteria: criteria,
      layouts: {
        labelFormat: labelFormat,
        tableLayout: viewerIdx === 0 ? tableLayout : null,
        mapLayout: viewerIdx === 1 ? mapLayout : null,
        chartLayout: viewerIdx >= 2 ? chartLayout : null
      }
    });
  };

  const handleViewClose = () => {
    setViewVisibility(false);
    setTemplate(initialTemplate);
  };

  const handleViewSubmit = () => {
    onViewTemplateSubmit(
      {
        type: "view",
        datasetId: template.datasetId,
        title: template.title,
        defaultView: template.defaultView,
        criteria: template.criteria,
        layouts: JSON.stringify(template.layouts)
      },
      true
    );
    setViewVisibility(false);
  };

  const handleTemplateOpen = defaultLanguage => {
    setTemplateVisibility(true);
    setTemplate({
      ...initialTemplate,
      datasetId: datasetId,
      title: {
        [defaultLanguage]: datasetTitle
      },
      defaultView: "table",
      criteria: criteria,
      layouts: {
        labelFormat: labelFormat,
        tableDefaultLayout: "custom",
        tableLayout: tableLayout,
        tableEmptyChar: (emptyChar !== null && emptyChar !== undefined) ? emptyChar : EMPTY_CHAR_DEFAULT,
        mapLayout: mapLayout,
        chartLayout: chartLayout
      },
      decimalNumber: (decimalPlaces !== null && decimalPlaces !== undefined) ? decimalPlaces : DECIMAL_PLACES_DEFAULT,
      decimalSeparator: {
        [defaultLanguage]: (initialDecimalSeparator || DECIMAL_SEPARATOR_DEFAULT)
      }
    });
  };

  const handleTemplateClose = () => {
    setTabId(0);
    setTemplateVisibility(false);
    setTemplate(initialTemplate);
  };

  const handleTemplateSubmit = () => {
    onViewTemplateSubmit(
      {
        ...template,
        type: "template",
        criteria: template.criteria,
        layouts: JSON.stringify(template.layouts)
      },
      false
    );
    setTabId(0);
    setTemplateVisibility(false);
    setTemplate(initialTemplate);
  };

  const datasetAttributes = [];
  const dataset = (jsonStat?.extension?.attributes?.dataSet || []);
  const datasetIndexes = (jsonStat?.extension?.attributes?.index?.dataSet || []);

  if (dataset && dataset.length > 0 && datasetIndexes && datasetIndexes.length > 0) {
    (datasetIndexes || []).forEach((el, idx) => {
      if (el !== null && !(hiddenAttributes || []).includes(dataset[idx].id)) {
        datasetAttributes.push({
          id: (dataset[idx].name || dataset[idx].id),
          value: (dataset[idx].values[el].name || dataset[idx].values[el].id)
        })
      }
    })
  }

  const otherAttributes = [];
  const series = (jsonStat?.extension?.attributes?.series || []);
  const seriesIndexes = (jsonStat?.extension?.attributes?.index?.series || []);

  if (series && series.length > 0 && seriesIndexes && seriesIndexes.length > 0) {
    seriesIndexes
      .filter(({coordinates}) => (coordinates || []).filter(c => c !== null).length > 1)
      .forEach(({coordinates, attributes}) => {
        const dimsId = [];
        const dimsValueId = [];
        coordinates.forEach((el, idx) => {
          if (el !== null) {
            const dim = jsonStat.id[idx];
            dimsId.push(dim);
            dimsValueId.push(jsonStat.dimension[dim].category.index[el]);
          }
        });
        const dims = dimsId.map((dim, idx) => ({
          id: getDimensionLabelFromJsonStat(jsonStat, dim, labelFormat),
          value: getDimensionValueLabelFromJsonStat(jsonStat, dim, dimsValueId[idx], labelFormat)
        }));
        const attrs = [];
        attributes.forEach((attr, idx) => {
          if (attr !== null && attr !== undefined && !(hiddenAttributes || []).includes(series[idx].id)) {
            attrs.push({
              id: (series[idx].name || series[idx].id),
              value: (series[idx].values[attr].name || series[idx].values[attr].id)
            })
          }
        });
        if (dims.length > 0 && attrs.length > 0) {
          otherAttributes.push({
            dims: dims,
            attributes: attrs
          });
        }
      });
  }

  const currentFilters = (viewers[viewerIdx].key === 0 && tableLayout?.filters)
    ? tableLayout.filters
    : (viewers[viewerIdx].key === 1 && mapLayout?.filters)
      ? mapLayout.filters
      : (viewers[viewerIdx].key >= 2 && chartLayout?.filters)
        ? chartLayout.filters
        : [];

  const subtitle = [];
  if (jsonStat && currentFilters) {
    currentFilters.forEach(filter => {
      if (jsonStat.size[jsonStat.id.indexOf(filter)] === 1) {
        subtitle.push({
          dim: filter,
          dimLabel: getDimensionLabelFromJsonStat(jsonStat, filter, labelFormat),
          value: jsonStat.dimension[filter].category.index[0],
          valueLabel: getDimensionValueLabelFromJsonStat(jsonStat, filter, jsonStat.dimension[filter].category.index[0], labelFormat)
        });
      }
    })
  }

  const dimensionAttributesMap = getDimensionAttributeMap(jsonStat, hiddenAttributes);

  return (
    <Fragment>

      <Grid container className={classes.root}>
        <Grid container justify="space-between" alignItems="flex-start">
          <Grid item id="dataset-viewer-header__title" className={classes.headerTitle}>
            <Grid container alignItems="flex-start">
              <Grid item>
                <Tooltip title={t("scenes.dataViewer.header.action.goHome.tooltip")}>
                  <IconButton color="primary" onClick={() => goToNode(nodeCode)}>
                    <HomeIcon/>
                  </IconButton>
                </Tooltip>
              </Grid>
              <Grid container style={{width: "calc(100% - 48px)"}}>
                <Grid item className={classes.title}>
                  <div style={{display: "table", minHeight: 48}}>
                    <div style={{display: "table-cell", verticalAlign: "middle"}}>
                      <span style={{marginRight: 4}}>{datasetTitle}</span>
                      {notes && notes.length > 0 && (
                        <Tooltip title={t("scenes.dataViewer.header.action.information.tooltip")}>
                          <IconButton
                            id="dataset-attributes-btn"
                            className={classes.titleAction}
                            onClick={() => setNotesVisibility(true)}
                          >
                            <InfoIcon/>
                          </IconButton>
                        </Tooltip>
                      )}
                      {datasetAttributes.concat(otherAttributes).length > 0 && (
                        <Tooltip title={t("scenes.dataViewer.header.action.attributes.tooltip")}>
                          <IconButton
                            id="dataset-attributes-btn"
                            className={classes.titleAction}
                            onClick={() => setAttributesVisibility(true)}
                          >
                            <div className={classes.attributeIcon}>(*)</div>
                          </IconButton>
                        </Tooltip>
                      )}
                    </div>
                  </div>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid item id="dataset-viewer-header__actions" className={classes.headerActions}>
            <Grid container alignItems="center">
              {canViewTemplateOrAnnotationIcon(user, nodeId) && hasTemplateLayout && (
                <div className={classes.icon}>
                  <Tooltip title={t("scenes.dataViewer.header.action.hasTemplate.tooltip")}>
                    <InfoIcon/>
                  </Tooltip>
                </div>
              )}
              {canViewTemplateOrAnnotationIcon(user, nodeId) && viewerIdx === 0 && !hasTemplateLayout && hasAnnotationLayout && (
                <div className={classes.icon}>
                  <Tooltip title={t("scenes.dataViewer.header.action.hasAnnotationLayout.tooltip")}>
                    <InfoIcon/>
                  </Tooltip>
                </div>
              )}
              {canViewTimesLog(user, nodeId) && (
                <Grid item id="dataset-times-log-btn">
                  <Tooltip title={t("scenes.dataViewer.header.action.timesLog.tooltip")}>
                    <div>
                      <IconButton onClick={() => setTimingsVisibility(true)} disabled={timings === null}>
                        <AccessTimeIcon/>
                      </IconButton>
                    </div>
                  </Tooltip>
                </Grid>
              )}
              {(canSaveAsView(user) || canSaveTemplate(user, nodeId)) && (
                <Grid item id="dataset-save-btn">
                  <ButtonSelect
                    icon={<SaveIcon/>}
                    tooltip={t("scenes.dataViewer.header.action.save.tooltip")}
                    color="default"
                    disabled={!tableLayout && !chartLayout && !mapLayout}
                  >
                    {canSaveAsView(user) && (
                      <div onClick={() => handleViewOpen(defaultLanguage)}>
                        {t("scenes.dataViewer.header.action.save.values.view")}
                      </div>
                    )}
                    {canSaveTemplate(user, nodeId) && (
                      <div onClick={() => handleTemplateOpen(defaultLanguage)}>
                        {t("scenes.dataViewer.header.action.save.values.template")}
                      </div>
                    )}
                  </ButtonSelect>
                </Grid>
              )}
              {canShare(user) && (
                <Grid item id="dataset-share-btn">
                  <Tooltip title={t("scenes.dataViewer.header.action.share.tooltip")}>
                    <IconButton onClick={window.notImplemented.show}>
                      <ShareIcon/>
                    </IconButton>
                  </Tooltip>
                </Grid>
              )}
              <Grid item id="dataset-attachments-btn">
                <ButtonSelect
                  icon={<AttachFileIcon/>}
                  tooltip={t("scenes.dataViewer.header.action.attachments.tooltip")}
                  color="default"
                  disabled={!attachedFiles || attachedFiles.length === 0}
                >
                  {(attachedFiles || []).map((attachedFile, idx) => (
                    <div key={idx} onClick={() => window.open(attachedFile.url)}>
                      {`${attachedFile.url} (${attachedFile.format})`}
                    </div>
                  ))}
                </ButtonSelect>
              </Grid>
              {(downloadFormats && downloadFormats.length > 0 && (
                <Grid item id="dataset-export-btn">
                  <ButtonSelect
                    icon={<GetAppIcon/>}
                    tooltip={t("scenes.dataViewer.header.action.export.tooltip")}
                    color="default"
                  >
                    {downloadFormats.map(({format, label, extension}, idx) =>
                      <div key={idx} onClick={() => onDownloadSubmit(format, extension)}>{label}</div>
                    )}
                  </ButtonSelect>
                </Grid>
              ))}
            </Grid>
          </Grid>
        </Grid>
        <Grid item className={classes.subtitle} xs={12}>
          {subtitle && dimensionAttributesMap && subtitle.map(({dim, dimLabel, value, valueLabel}, idx) =>
            <div key={idx} style={{display: "inline-block", marginRight: 8}}>
              <div style={{display: "inline-block"}}>
                <b>{dimLabel}</b>: <i>{valueLabel}</i>
              </div>
              {dimensionAttributesMap[dim][value] && (
                <div style={{display: "inline-block", marginLeft: 4}}>
                  <Tooltip
                    title={
                      <div>
                        {dimensionAttributesMap[dim][value].attributes.map(({id, label, valueId, valueLabel}, idx) => (
                          <div
                            key={idx}>{`${label || id}: ${valueLabel || valueId}${valueLabel !== valueId ? ` [${valueId}]` : ''}`}</div>
                        ))}
                      </div>
                    }
                    placement="top"
                  >
                    <div>
                      {`[${dimensionAttributesMap[dim][value].ids.join(", ")}]`}
                    </div>
                  </Tooltip>
                </div>
              )}
              <div style={{display: "inline-block"}}>
                {idx < subtitle.length - 1 ? "," : ""}
              </div>
            </div>
          )}
        </Grid>
      </Grid>

      <Dialog
        open={isAttributesVisible}
        fullWidth
        maxWidth="md"
        onClose={() => setAttributesVisibility(false)}
      >
        <DialogTitle>
          {t("scenes.dataViewer.header.dialogs.attributes.title")}
        </DialogTitle>
        <DialogContent>
          <AttributeList
            datasetAttributes={datasetAttributes}
            otherAttributes={otherAttributes}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setAttributesVisibility(false)}>
            {t("commons.confirm.close")}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={isTimingsVisible}
        onClose={() => setTimingsVisibility(false)}
      >
        <DialogContent style={{width: 400}}>
          <div>
            {(() => {
              const toStamp = [];

              const TOTAL_TIME_KEY = "BackeEnd completed";
              const DOWNLOAD_SIZE_KEY = "NSI Response Download Size";

              const getRow = (key, value, isBold) => (
                <Grid container justify="space-between" spacing={2} key={key}>
                  <Grid item>
                    {isBold ? <b>{key}:</b> : `${key}:`}
                  </Grid>
                  <Grid item>
                    <b>{value}</b>
                  </Grid>
                </Grid>
              );

              const titleStyle = {
                width: "100%",
                height: 32,
                fontSize: 15
              };

              if (timings) {

                toStamp.push(getRow("Observation count", timings[OBSERVATION_COUNT_KEY], true));
                toStamp.push(<Divider style={{margin: "8px 0"}} key="divider3"/>);

                /** server side **/
                if (timings[SERVER_TIMINGS_KEY]) {
                  toStamp.push(
                    <div style={{...titleStyle, marginTop: 24}} key="server"><b><u>Server timings</u></b></div>
                  );
                  for (const key in timings[SERVER_TIMINGS_KEY]) {
                    if (timings[SERVER_TIMINGS_KEY].hasOwnProperty(key)) {
                      if (key !== TOTAL_TIME_KEY && key !== DOWNLOAD_SIZE_KEY) {
                        toStamp.push(
                          getRow(key, timings[SERVER_TIMINGS_KEY][key])
                        )
                      }
                    }
                  }
                  toStamp.push(<Divider style={{margin: "8px 0"}} key="divider1"/>);
                  toStamp.push(getRow("Total", timings[SERVER_TIMINGS_KEY][TOTAL_TIME_KEY], true));
                  toStamp.push(<Divider style={{margin: "8px 0"}} key="divider2"/>);
                  if (timings[SERVER_TIMINGS_KEY][DOWNLOAD_SIZE_KEY]) {
                    toStamp.push(getRow(DOWNLOAD_SIZE_KEY, timings[SERVER_TIMINGS_KEY][DOWNLOAD_SIZE_KEY]));
                  }
                }

                /** client side **/
                if (timings[GENERATING_HTML_TIME_KEY] !== null && timings[GENERATING_HTML_TIME_KEY] !== undefined) {
                  toStamp.push(
                    <div style={{...titleStyle, marginTop: 24}} key="client"><b><u>Client timings</u></b></div>
                  );
                  toStamp.push(getRow("Generating HTML", `${timings[GENERATING_HTML_TIME_KEY]}ms`));
                }
              }

              return toStamp
            })()}
          </div>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={() => setTimingsVisibility(false)}>
            {t("commons.confirm.close")}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={isViewVisible}
        onClose={handleViewClose}
      >
        <DialogTitle>
          {t("scenes.dataViewer.header.dialogs.saveView.title")}
        </DialogTitle>
        <DialogContent style={{width: 480}}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <FormControl fullWidth className={classes.field}>
                <I18nTextField
                  label="Title"
                  required
                  variant="outlined"
                  value={template.title}
                  onChange={value => setTemplate({...template, title: value})}
                />
              </FormControl>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleViewClose}>
            {t("commons.confirm.cancel")}
          </Button>
          <Button
            color="primary"
            autoFocus
            onClick={handleViewSubmit}
            disabled={!validateI18nObj(template.title)}
          >
            {t("commons.confirm.save")}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={isTemplateVisible}
        fullScreen
        onClose={handleTemplateClose}
      >
        <DialogTitle>
          {t("scenes.dataViewer.header.dialogs.saveTemplate.title")}
        </DialogTitle>
        <DialogContent>
          <AppBar position="static" color="default">
            <Tabs
              value={tabId}
              variant="scrollable"
              scrollButtons="auto"
              indicatorColor="primary"
              textColor="primary"
              onChange={(event, newValue) => setTabId(newValue)}
            >
              <Tab key={0} label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.label")}/>
              <Tab key={1} label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.table.label")}/>
              <Tab key={2} label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.chart.label")}/>
              <Tab key={3} label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.map.label")}/>
            </Tabs>
          </AppBar>
          <div className={classes.tabContent}>
            {tabId === 0 && (
              <Fragment>
                <Card variant="outlined">
                  <CardHeader
                    title={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.title")}
                    titleTypographyProps={{variant: "subtitle1"}}
                  />
                  <Grid container spacing={2} className={classes.card}>
                    <Grid item xs={6}>
                      <FormControl fullWidth className={classes.field}>
                        <I18nTextField
                          label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.title")}
                          required
                          variant="outlined"
                          value={template.title}
                          onChange={value => setTemplate({...template, title: value})}
                        />
                      </FormControl>
                    </Grid>
                    <Grid item xs={6}>
                      <FormControl fullWidth className={classes.field}>
                        <TextField
                          select
                          label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.defaultView")}
                          defaultValue="table"
                          value={template.defaultView}
                          variant="outlined"
                          onChange={ev => setTemplate({...template, defaultView: ev.target.value})}
                        >
                          {viewers.map(({type, title}, idx) =>
                            <MenuItem key={idx} value={type}>{title}</MenuItem>
                          )}
                        </TextField>
                      </FormControl>
                    </Grid>
                    <Grid item xs={6}>
                      <FormControlLabel
                        label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.enableCriteria")}
                        className={classes.field}
                        control={
                          <Checkbox
                            checked={template.enableCriteria}
                            onChange={(ev, value) => setTemplate({...template, enableCriteria: value})}
                          />
                        }
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <FormControlLabel
                        label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.enableLayout")}
                        className={classes.field}
                        control={
                          <Checkbox
                            checked={template.enableLayout}
                            onChange={(ev, value) => setTemplate({...template, enableLayout: value})}
                          />
                        }
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <FormControlLabel
                        label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.hideDimensions") + " (*)"}
                        className={classes.field}
                        control={
                          <Checkbox
                            checked={template.hideDimensions}
                            onChange={(ev, value) => setTemplate({...template, hideDimensions: value})}
                            disabled
                          />
                        }
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <FormControlLabel
                        label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.enableVariation") + " (*)"}
                        className={classes.field}
                        control={
                          <Checkbox
                            checked={true} // template.enableVariation
                            onChange={(ev, value) => setTemplate({...template, enableVariation: value})}
                            disabled
                          />
                        }
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <FormControl fullWidth className={classes.field}>
                        <TextField
                          label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.decimalPlaces")}
                          value={template.decimalNumber}
                          variant="outlined"
                          required
                          onChange={ev => setTemplate({...template, decimalNumber: ev.target.value})}
                        />
                      </FormControl>
                    </Grid>
                    <Grid item xs={6}>
                      <FormControl fullWidth className={classes.field}>
                        <I18nTextField
                          select
                          label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.decimalSeparator.label")}
                          value={template.decimalSeparator}
                          defaultValue="."
                          variant="outlined"
                          required
                          onChange={value => setTemplate({...template, decimalSeparator: value})}
                        >
                          <MenuItem value=".">
                            {t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.decimalSeparator.values.dot")}
                          </MenuItem>
                          <MenuItem value=",">
                            {t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.decimalSeparator.values.comma")}
                          </MenuItem>
                        </I18nTextField>
                      </FormControl>
                    </Grid>
                    <Grid item xs={6}>
                      <FormControl fullWidth className={classes.field}>
                        <TextField
                          label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.general.form.filterOnLastPeriods") + " (*)"}
                          value={template.lastPeriods}
                          type="number"
                          variant="outlined"
                          onChange={ev => setTemplate({...template, lastPeriods: ev.target.value})}
                          disabled
                        />
                      </FormControl>
                    </Grid>
                  </Grid>
                </Card>
                <Card variant="outlined" style={{marginTop: 16}}>
                  <CardHeader
                    title={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.table.title")}
                    titleTypographyProps={{variant: "subtitle1"}}
                  />
                  <Grid container spacing={2} className={classes.card}>
                    <Grid item xs={6}>
                      <FormControl fullWidth className={classes.field}>
                        <TextField
                          label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.cards.table.form.emptyCellPlaceholder")}
                          value={template.layouts.tableEmptyChar}
                          variant="outlined"
                          required
                          onChange={ev => setTemplate({
                            ...template,
                            layouts: {
                              ...template.layouts,
                              tableEmptyChar: ev.target.value
                            }
                          })}
                        />
                      </FormControl>
                    </Grid>
                    <Grid item xs={6}/>
                  </Grid>
                </Card>
                <div
                  style={{marginTop: 16}}>{"( *): " + t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.options.notImplemented")}</div>
              </Fragment>
            )}
            {tabId === 1 && (
              <Fragment>
                <Grid container spacing={2}>
                  <Grid item xs={12}>
                    <FormControl fullWidth className={classes.field}>
                      <TextField
                        select
                        label={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.table.form.defaultLayout.label")}
                        value={template.layouts.tableDefaultLayout}
                        variant="outlined"
                        onChange={ev => setTemplate({
                          ...template,
                          layouts: {
                            ...template.layouts,
                            tableDefaultLayout: ev.target.value,
                            tableLayout: ev.target.value === "custom"
                              ? tableLayout
                              : null
                          }
                        })}
                      >
                        <MenuItem value="custom">
                          {t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.table.form.defaultLayout.values.custom")}
                        </MenuItem>
                        <MenuItem value="default">
                          {t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.table.form.defaultLayout.values.default")}
                        </MenuItem>
                      </TextField>
                    </FormControl>
                  </Grid>
                </Grid>
                <div className={classes.viewer}>
                  {template.layouts.tableDefaultLayout === "custom"
                    ? (
                      <JsonStatTable
                        jsonStat={jsonStat}
                        layout={tableLayout}
                        labelFormat={labelFormat}
                        decimalSeparator={localizeI18nObj(template.decimalSeparator, defaultLanguage, languages)}
                        decimalPlaces={template.decimalNumber}
                        emptyChar={template.layouts.tableEmptyChar}
                        hiddenAttributes={hiddenAttributes}
                      />
                    )
                    : (
                      <CustomEmpty
                        text={t("scenes.dataViewer.header.dialogs.saveTemplate.content.tabs.table.form.defaultLayout.defaultPlaceholder")}/>
                    )
                  }
                </div>
              </Fragment>
            )}
            {tabId === 2 && (
              <CustomEmpty text={t("commons.notImplemented")}/>
            )}
            {tabId === 3 && (
              <CustomEmpty text={t("commons.notImplemented")}/>
            )}
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleTemplateClose}>
            {t("commons.confirm.cancel")}
          </Button>
          <Button
            color="primary"
            autoFocus
            onClick={handleTemplateSubmit}
            disabled={(
              !validateI18nObj(template.title) ||
              !validateI18nObj(template.decimalSeparator) ||
              !Number.isInteger(Number(template.decimalNumber)) || Number(template.decimalNumber) < 0 || Number(template.decimalNumber) > 20
            )}
          >
            {t("commons.confirm.save")}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={isNotesVisible}
        fullWidth
        maxWidth="md"
        onClose={() => setNotesVisibility(false)}
      >
        <DialogTitle>
          {t("scenes.dataViewer.header.dialogs.notes.title")}
        </DialogTitle>
        <DialogContent>
          {notes}
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setNotesVisibility(false)}>
            {t("commons.confirm.close")}
          </Button>
        </DialogActions>
      </Dialog>

    </Fragment>
  )
}

export default compose(
  withStyles(styles),
  connect(mapStateToProps)
)(DataViewerHeader)