import React, {Component, Fragment} from 'react';
import NodeHeader from "../node-header";
import {Link, withStyles} from "@material-ui/core";
import Page from "../page";
import Container from "@material-ui/core/Container";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import {goToData, goToDatasets, goToNode} from "../../links";
import CardActionArea from "@material-ui/core/CardActionArea";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import IconButton from "@material-ui/core/IconButton";
import Divider from "@material-ui/core/Divider";
import ListIcon from '@material-ui/icons/List';
import GridOnIcon from '@material-ui/icons/GridOn';
import Breadcrumbs from "@material-ui/core/Breadcrumbs";
import ResultTree from "./ResultTree";
import Chip from "@material-ui/core/Chip";
import {withTranslation} from "react-i18next";
import {compose} from "redux";
import FolderIcon from "@material-ui/icons/Folder";

const styles = theme => ({
  root: {
    width: "100%",
    height: "100%"
  },
  fullWidthContainer: {
    width: "100%",
    paddingTop: 64,
    backgroundColor: "white",
    minHeight: "100%"
  },
  container: {
    width: 1024,
  },
  section: {
    paddingTop: theme.spacing(3)
  },
  results: {
    marginTop: theme.spacing(1)
  },
  cubeIcon: {
    width: 24,
    height: 24
  },
  collapsedDescription: {
    marginTop: 8,
    height: 49,
    overflow: "hidden"
  },
  expandedDescription: {
    marginTop: 8
  },
  resultSource: {
    fontSize: 14,
    color: "rgba(0, 0, 0, 0.54)",
  },
  expansion: {},
  toolbar: {
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(1)
  },
  pager: {
    marginTop: theme.spacing(2)
  },
  cardContent: {
    padding: "16px !important"
  },
  subCat: {
    padding: theme.spacing(1)
  },
  row: {
    marginBottom: theme.spacing(1)
  }
});

class Results extends Component {

  state = {
    isDialogOpen: false,
    expandedIndexes: [],
    configIsListMode: this.props.hub.nodes
      .find(({code}) => code.toLowerCase() === this.props.nodeCode.toLowerCase())
      .catalogNavigationMode === "List",
    isListMode:
      this.props.hub.nodes
        .find(({code}) => code.toLowerCase() === this.props.nodeCode.toLowerCase())
        .catalogNavigationMode === "List",
    isA11yMode: false
  };

  static getDerivedStateFromProps(props, state) {

    const configIsListMode = props.hub.nodes
      .find(({code}) => code.toLowerCase() === props.nodeCode.toLowerCase())
      .catalogNavigationMode === "List";

    if (configIsListMode !== state.configIsListMode) {
      return {
        ...state,
        configIsListMode,
        isListMode: configIsListMode
      };
    } else {
      return null;
    }
  }

  onDialogOpen = () => this.setState({isDialogOpen: true});

  onDialogClose = () => this.setState({isDialogOpen: false});

  expand = index =>
    this.setState({
      expandedIndexes: [...this.state.expandedIndexes, index]
    });

  collapse = index =>
    this.setState({
      expandedIndexes: [...this.state.expandedIndexes.filter(i => i !== index)]
    });

  onListModeToggle = () => {
    this.setState({isListMode: !this.state.isListMode});
  };

  onA11yModeToggle = () => {
    this.setState({isA11yMode: !this.state.isA11yMode});
  };

  render() {

    const {
      classes,
      query,
      hub,
      catalog,
      node,
      nodeCode,
      categoryPath,
      datasets,
      subCategories,
      filters,
      categoryGroupsWithCount,
      uncategorizedDatasetsCount,
      t
    } = this.props;

    const {isDialogOpen, expandedIndexes, isListMode, isA11yMode} = this.state;

    const nodeMinimalInfo = hub.nodes.find(({code}) => code.toLowerCase() === nodeCode.toLowerCase());

    let categoryPathLabels = [];
    if (categoryPath) {
      if (categoryPath[0] === "uncategorized") {
        categoryPathLabels = [t("commons.catalog.uncategorized")];
      } else {

        let category;

        if (catalog.categoryGroups.length > 1) {
          category = catalog.categoryGroups.find(({id}) => id === categoryPath[0]);
        } else {
          category = catalog.categoryGroups[0].categories.find(({id}) => id === categoryPath[0]);
        }

        categoryPathLabels.push(category.label);
        for (let i = 1; i < categoryPath.length; i++) {
          category = (category.childrenCategories || category.categories).find(({id}) => id === categoryPath[i]);
          categoryPathLabels.push(category.label);
        }
      }
    }

    return (
      <Page>
        <NodeHeader
          hub={hub.hub}
          query={query}
          nodes={hub.nodes}
          catalog={catalog}
          shortName={nodeMinimalInfo.code}
          title={nodeMinimalInfo.name}
          node={node}
          nodeId={nodeMinimalInfo.nodeId}
          selectedCategoryPath={categoryPath}
        />
        <div className={classes.fullWidthContainer}>
          <Container className={classes.container}>
            <Grid container justify="space-between" alignItems="center" className={classes.toolbar}>
              <Grid item>
                {query ?
                  (
                    <Typography variant={"h5"}>
                      {t("scenes.results.searchTitle", {query})}
                    </Typography>
                  )
                  : (
                    <Breadcrumbs>
                      <Link underline="none" onClick={() => {
                        goToNode(node.code);
                        window.location.reload();
                      }} style={{cursor: "pointer"}}
                      >
                        <Typography>
                          {nodeCode.toUpperCase()}
                        </Typography>
                      </Link>
                      {categoryPath.map((id, idx) => {
                        if (idx >= (catalog && catalog.categoryGroups.length === 1 ? 0 : 1)) {
                          return (
                            <Link
                              key={idx}
                              underline="none"
                              onClick={
                                idx >= (
                                  catalog && catalog.categoryGroups.length === 1
                                    ? 0
                                    : 1
                                )
                                  ? () => goToDatasets(node.code, categoryPath.slice(0, idx + 1))
                                  : null
                              }
                              style={idx < categoryPath.length - 1 ? {cursor: "pointer"} : undefined}
                            >
                              <Typography>
                                {id === "uncategorized"
                                  ? t("commons.catalog.uncategorized")
                                  : categoryPathLabels[idx]
                                }
                              </Typography>
                            </Link>
                          );
                        } else {
                          return (
                            <Typography key="idx" color="primary">
                              {id === "uncategorized"
                                ? t("commons.catalog.uncategorized")
                                : categoryPathLabels[idx]
                              }
                            </Typography>
                          );
                        }
                      })}
                    </Breadcrumbs>
                  )}
              </Grid>
              <Grid item>
                <Grid container alignItems="center" justify="flex-end" spacing={1}>
                  <Grid item>
                    {datasets.length > 0 && (
                      <IconButton onClick={this.onListModeToggle}>
                        {isListMode
                          ? <GridOnIcon/>
                          : <ListIcon/>
                        }
                      </IconButton>
                    )}
                  </Grid>
                  {/*<Grid item>
                    <label htmlFor="results__a11y-mode-toggle" style={{display: "none"}}>
                      {t("scenes.results.a11yModeToggle.label")}
                    </label>
                    <FormControlLabel
                      label={t("scenes.results.a11yModeToggle.label")}
                      control={
                        <Switch
                          checked={isA11yMode}
                          onChange={this.onA11yModeToggle}
                          color="primary"
                          name="checkedB"
                          id="results__a11y-mode-toggle"
                        />
                      }
                    />
                  </Grid>*/}
                </Grid>
              </Grid>
            </Grid>
            <Grid container spacing={2} className={classes.results}>
              {query && datasets.length > 0 && (
                <Grid item style={{width: 256, transform: "translateX(-24px)"}}>
                  <ResultTree
                    query={query} filters={filters} catalog={catalog} node={node}
                    datasets={datasets}
                    categoryGroupsWithCount={categoryGroupsWithCount}
                    uncategorizedDatasetsCount={uncategorizedDatasetsCount}
                  />
                </Grid>
              )}
              <Grid item style={{width: query ? `calc(100% - 256px)` : "100%"}}>
                {subCategories && subCategories.length
                  ? (
                      <Grid container spacing={2} className={classes.row}>
                        {subCategories.map(({id, label, datasetIdentifiers}) =>
                          <Grid item key={id}>
                            <Card className={classes.subCat}>
                              <CardActionArea
                                onClick={() => goToDatasets(node.code.toLowerCase(), [...categoryPath, id])}>
                                <Typography variant="body1">
                                  <Grid container spacing={1}>
                                    <Grid item>
                                      <FolderIcon/>
                                    </Grid>
                                    <Grid item>
                                      {label} {datasetIdentifiers && datasetIdentifiers.length ? `(${datasetIdentifiers.length})` : ''}
                                    </Grid>
                                  </Grid>
                                </Typography>
                              </CardActionArea>
                            </Card>
                          </Grid>
                        )}
                      </Grid>
                  )
                  : null}
                {query && datasets.length
                  ? (
                    <Typography variant="body1" className={classes.row}>
                      {datasets.length > 1
                        ? t("scenes.results.datasetsCount.plural", {datasetsCount: datasets.length})
                        : t("scenes.results.datasetsCount.singular", {datasetsCount: datasets.length})
                      }:
                    </Typography>
                  ) : null
                }
                <Grid container spacing={2}>
                  {datasets.length > 0
                    ? datasets.map((dataset, i) => {

                      const isExpanded = expandedIndexes.includes(i);

                      const header =
                        <CardActionArea
                          onClick={() => goToData(node.code, dataset.categoryPath, dataset.identifier, undefined, isA11yMode)}>
                          <Typography variant="h6">
                            {dataset.title}
                          </Typography>
                          {dataset.source && (
                            <Typography variant="body1" className={classes.resultSource}>
                              {dataset.source}
                            </Typography>
                          )}
                        </CardActionArea>;

                      const description = dataset.description || (dataset.keywords && dataset.keywords.length > 0)
                        ? (
                          <Typography
                            component='div'
                            variant="body1"
                            className={isExpanded ? classes.expandedDescription : classes.collapsedDescription}>
                            <div dangerouslySetInnerHTML={{__html: dataset.description}}/>
                            {dataset.keywords && dataset.keywords.length > 0 && (
                              <Grid container spacing={1}>
                                {dataset.keywords.map((keyword, index) =>
                                  <Grid item key={index}>
                                    <Chip
                                      label={keyword}
                                      size="small"
                                    />
                                  </Grid>
                                )}
                              </Grid>
                            )}
                          </Typography>
                        )
                        : null;

                      const expandIcon = <Grid container justify="flex-end">
                        <Grid item>
                          <IconButton
                            onClick={() => isExpanded ? this.collapse(i) : this.expand(i)}
                          >
                            {isExpanded
                              ? <ExpandLessIcon/>
                              : <ExpandMoreIcon/>
                            }
                          </IconButton>
                        </Grid>
                      </Grid>;

                      /*    const viewButton = <Button onClick={() => goToData(node.code, dataset.categoryPath, dataset.identifier)}
                                                     color="primary">View</Button>;*/

                      /*   const downloadButton = <Button color="secondary" onClick={this.onDialogOpen}>
                           Download
                         </Button>;
       */
                      return (
                        <Grid item key={i} xs={isListMode ? 12 : 4}>
                          <Card>
                            {isListMode
                              ? (
                                <CardContent className={classes.cardContent}>
                                  <Grid container>
                                    <Grid item xs={12}>
                                      {header}
                                    </Grid>
                                  </Grid>
                                  {description}
                                  {description && expandIcon}
                                </CardContent>
                              )
                              : (
                                <Fragment>
                                  <CardContent>
                                    {header}
                                    {description}
                                    {description && expandIcon}
                                  </CardContent>
                                  <Divider/>
                                  {/*<CardActions>
                                <Grid container justify="space-between">
                                  <Grid item>
                                    {viewButton}
                                  </Grid>
                                </Grid>
                              </CardActions>*/}
                                </Fragment>
                              )}
                          </Card>
                        </Grid>
                      );
                    })
                    : <Grid container><Grid
                      item>{t("scenes.results.datasetsCount.none")}</Grid></Grid>}
                </Grid>
              </Grid>
            </Grid>
            {/* pagination */}
            {/* <Grid container justify="flex-end" className={classes.pager}>
              <Grid item>
                <Pagination count={10}/>
              </Grid>
            </Grid>*/}
            <Dialog open={isDialogOpen} onClose={this.onDialogClose}>
              <DialogTitle>Download</DialogTitle>
              <DialogContent>
                <Button color="primary" onClick={this.onDialogClose}>
                  CSV
                </Button>
                <Button color="primary" onClick={this.onDialogClose}>
                  SDMX
                </Button>
              </DialogContent>
              <DialogActions>
                <Button onClick={this.onDialogClose}>
                  {t("commons.confirm.cancel")}
                </Button>
              </DialogActions>
            </Dialog>
          </Container>
        </div>
      </Page>
    );
  }
}

export default compose(
  withStyles(styles),
  withTranslation()
)(Results);