import React, {Fragment, useEffect} from 'react';
import {Route, Switch} from "react-router";
import Home from "../components/home";
import {connect} from "react-redux";
import {fetchHub} from "../state/hub/hubActions";
import NodeDomain from "./NodeDomain";
import {HashRouter} from "react-router-dom";
import DashboardsDomain from "./DashboardsDomain";

const mapStateToProps = state => ({
  hub: state.hub
});

const mapDispatchToProps = dispatch => ({
  fetchHub: () => dispatch(fetchHub())
});

const HubDomain = ({hub, fetchHub}) => {

  useEffect(() => {
    if (!hub) {
      fetchHub();
    }
  }, [hub, fetchHub]);

  return (
    <Fragment>
      {hub && (
        <HashRouter>
          <Switch>
            <Route path='/' exact render={props => <Home {...props} hub={hub}/>}/>
            <Route
              path='/dashboards'
              exact
              render={props => <DashboardsDomain {...props} hub={hub}/>}
            />
            <Route
              path='/dashboards/:dashboardId'
              render={props => <DashboardsDomain {...props} hub={hub} dashboardId={props.match.params.dashboardId}/>}
            />
            <Route path='/:nodeCode'
                   render={props =>
                     <NodeDomain
                       {...props}
                       hub={hub}
                       nodeCode={props.match.params.nodeCode}
                     />
                   }
            />
          </Switch>
        </HashRouter>
      )}
    </Fragment>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(HubDomain);
