import {Link, withStyles} from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Typography from "@material-ui/core/Typography";
import React, {Component} from "react";
import Grid from "@material-ui/core/Grid";
import UserSelect from "../user-select";
import Drawer from "@material-ui/core/Drawer";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import Divider from "@material-ui/core/Divider";
import CategoriesTree from "../categories-tree";
import {withRouter} from "react-router";
import SettingsSelect from "../settings-select";
import SearchDialog from "../search-dialog";
import ButtonSelect from "../button-select";
import {goToDatasetsSearch, goToHome, goToNode, goToNodeDashboards} from "../../links";
import I18nInputAdornmentSelect from "../i18n-input-adornment-select";
import HomeIcon from '@material-ui/icons/Home';
import {compose} from "redux";
import {connect} from "react-redux";
import OtherSettingsSelect from "../other-settings-select";
import DashboardIcon from '@material-ui/icons/Dashboard';
import {
  canClearServerCache,
  canDisplayAppSettingsForm,
  canDisplayNodesSettingsForm,
  canDisplayUsersSettingsForm,
  canDisplayUserViews,
  canGetQueryLog,
  canManagePersonalDashboards
} from "../../utils/user";
import {setAppLanguage} from "../../state/app/appActions";
import {withTranslation} from "react-i18next";

const styles = theme => ({
  root: {
    position: "fixed",
    width: "100%",
    zIndex: theme.zIndex.appBar
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
    color: "white"
  },
  leftToolbar: {
    zIndex: theme.zIndex.appBar
  },
  leftContainer: {
    width: "calc((100% - 296px)/2)"
  },
  rightContainer: {
    width: "calc((100% - 296px)/2)"
  },
  drawer: {
    minWidth: 448
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  drawerHeaderTitle: {
    position: "absolute",
    left: theme.spacing(2),
  },
  drawerCategories: {
    margin: theme.spacing(2)
  },
  drawerCategoriesTitle: {
    marginBottom: theme.spacing(2)
  },
  goToHome: {
    margin: theme.spacing(2, 2, 0, 2)
  },
  centerContainer: {
    width: 280,
    textAlign: "center"
  },
  sistanLogo: {
    width: 32,
    height: 32,
    transform: "translateY(3px)"
  }
});


const mapStateToProps = state => ({
  baseURL: state.config.baseURL,
  user: state.user,
  language: state.app.language
});

const mapDispatchToProps = dispatch => ({
  onLanguageChange: (language, nodeId) => dispatch(setAppLanguage(language, nodeId))
});

class NodeHeader extends Component {

  state = {
    isDrawerOpen: this.props.defaultTreeOpen || false
  };

  onDrawerOpen = () => {
    this.setState({
      isDrawerOpen: true
    });
  };

  onDrawerClose = () => {
    this.setState({
      isDrawerOpen: false
    });
  };

  render() {

    const {
      classes,
      selectedCategoryPath,
      shortName,
      title,
      defaultAppConfigOpen,
      defaultUsersConfigOpen,
      defaultNodesConfigOpen,
      defaultNodeConfigOpen,
      nodes,
      noNode,
      catalog,
      node,
      query,
      hub,
      baseURL,
      user,
      onLanguageChange,
      language,
      t
    } = this.props;

    const {isDrawerOpen} = this.state;

    return (
      <div className={classes.root} id="node-header">
        <AppBar position="static" color="primary">
          <Grid container justify="space-between" alignItems="center">
            <Grid item className={classes.leftContainer}>
              {!noNode && (
                <Toolbar className={classes.leftToolbar}>
                  <Grid container spacing={1} alignItems="center">
                    <Grid item>
                      <IconButton
                        className={classes.menuButton}
                        edge="start"
                        color="inherit"
                        onClick={this.onDrawerOpen}
                      >
                        <MenuIcon/>
                      </IconButton>
                    </Grid>
                    <Grid item>
                      <ButtonSelect
                        value={
                          <Typography variant="h6" color="inherit">
                            {shortName}
                          </Typography>
                        }
                      >
                        {nodes.sort((a, b) => a.order - b.order).map(({code, name}, i) =>
                          <span key={i} onClick={() =>
                            goToNode(code.toLowerCase())
                          }>
                            {code} - {name}
                          </span>
                        )}
                      </ButtonSelect>
                    </Grid>
                  </Grid>
                </Toolbar>
              )}
            </Grid>
            <Grid item className={classes.centerContainer}>
              <Link onClick={goToHome} style={{cursor: "pointer"}}>
                <Grid container spacing={2} alignItems="center" justify="center">
                  {hub.logoURL && (
                    <Grid item>
                      <img src={baseURL + hub.logoURL} alt={hub.name} className={classes.sistanLogo}/>
                    </Grid>
                  )}
                  <Grid item>
                    <Typography variant="h6" color="secondary">
                      {hub.name}
                    </Typography>
                  </Grid>
                </Grid>
              </Link>
            </Grid>
            <Grid item className={classes.rightContainer}>
              <Toolbar>
                <Grid container justify="flex-end" alignItems="center" spacing={1}>
                  {!noNode && (
                    <div>
                      <SearchDialog
                        query={query}
                        modalWidth={320}
                        dialogTop={56}
                        onSubmit={value => goToDatasetsSearch(node.code, value)}
                      />
                    </div>
                  )}
                  <Grid item id="language-btn">
                    <I18nInputAdornmentSelect
                      onChange={val => onLanguageChange(val, node ? node.nodeId : null)}
                      value={language}
                    />
                  </Grid>
                  {!noNode && node && node.dashboards && node.dashboards.length > 0 && (
                    <Grid item id="dashboards-btn">
                      <IconButton onClick={() => goToNodeDashboards(node.code)} color="inherit">
                        <DashboardIcon/>
                      </IconButton>
                    </Grid>
                  )}
                  {(canDisplayAppSettingsForm(user) ||
                    canDisplayUsersSettingsForm(user) ||
                    canDisplayNodesSettingsForm(user) ||
                    canGetQueryLog(user) ||
                    canClearServerCache(user)
                  ) && (
                    <Grid item id="admin-settings-btn">
                      <SettingsSelect
                        defaultAppOpen={defaultAppConfigOpen}
                        defaultNodesOpen={defaultNodesConfigOpen}
                        defaultUsersOpen={defaultUsersConfigOpen}
                        defaultNodeOpen={defaultNodeConfigOpen}
                        nodes={nodes}
                      />
                    </Grid>
                  )}
                  {(canDisplayUserViews(user) || canManagePersonalDashboards(user)) && (
                    <Grid item id="other-settings-btn">
                      <OtherSettingsSelect nodes={nodes}/>
                    </Grid>
                  )}
                  <Grid item id="user-settings-btn">
                    <UserSelect/>
                  </Grid>
                </Grid>
              </Toolbar>
            </Grid>
          </Grid>
        </AppBar>
        {!noNode && (
          <Drawer
            anchor="left"
            open={isDrawerOpen}
            classes={{
              paper: classes.drawer
            }}
            onClose={this.onDrawerClose}
          >
            <div className={classes.drawerHeader}>
              <Typography variant="h6" className={classes.drawerHeaderTitle}>
                {title}
              </Typography>
              <IconButton onClick={this.onDrawerClose}>
                <ChevronLeftIcon/>
              </IconButton>
            </div>
            <Divider/>
            <div className={classes.goToHome}>
              <Link underline="none" onClick={() => {
                goToNode(node.code);
                window.location.reload();
              }} style={{cursor: "pointer"}}>
                <Grid container spacing={1}>
                  <Grid item>
                    <HomeIcon/>
                  </Grid>
                  <Grid item>
                    <Typography>
                      {t("components.header.drawer.backToNodeHome")}
                    </Typography>
                  </Grid>
                </Grid>
              </Link>
            </div>
            {catalog && (
              <div className={classes.drawerCategories}>
                <div className={classes.drawerCategoriesTitle}>
                  <Typography>
                    {t("components.header.drawer.categoriesTitle")}
                  </Typography>
                </div>
                <CategoriesTree selectedCategoryPath={selectedCategoryPath} catalog={catalog} node={node}
                                onClose={this.onDrawerClose}/>
              </div>
            )}
          </Drawer>
        )}
      </div>
    );
  }
}

export default compose(
  withStyles(styles),
  withTranslation(),
  connect(mapStateToProps, mapDispatchToProps),
  withRouter
)(NodeHeader);