import React, {useEffect, useState} from 'react';
import Data from "../components/data";
import {connect} from "react-redux";
import {enableDatasetStructureFetch, fetchDatasetStructure, resetDataset} from "../state/dataset/datasetActions";
import Call from "../hocs/call";

const mapStateToProps = state => ({
  dataset: state.dataset,
  defaultLanguage: state.app.language,
  languages: state.app.languages
});

const mapDispatchToProps = dispatch => ({
  onFetchStructureEnable: datasetId => dispatch(enableDatasetStructureFetch(datasetId)),
  fetchStructure: ({nodeId, datasetId, viewId, defaultLanguage, languages}) =>
    dispatch(fetchDatasetStructure(nodeId, datasetId, viewId, defaultLanguage, languages)),
  onDatasetReset: () => dispatch(resetDataset())
});

const DatasetDomain = ({
                         dataset,
                         defaultLanguage,
                         languages,
                         hub,
                         node,
                         catalog,
                         categoryPath,
                         isAccessible,
                         datasetId,
                         datasetTitle,
                         notes,
                         attachedFiles,
                         referenceMetadataUrl,
                         viewId,
                         onFetchStructureEnable,
                         fetchStructure,
                         onDatasetReset
                       }) => {

  const [isReset, setIsReset] = useState(false);

  useEffect(() => {
    if (!isReset && hub && node && catalog && categoryPath && datasetId && datasetTitle && dataset) {
      onFetchStructureEnable(datasetId);
      setIsReset(true);
    }
  }, [isReset, hub, node, catalog, categoryPath, datasetId, datasetTitle, dataset, onFetchStructureEnable, onDatasetReset, setIsReset]);

  return (
    <Call
      cb={fetchStructure}
      cbParam={{nodeId: node ? node.nodeId : null, datasetId, viewId, defaultLanguage, languages}}
      disabled={!node || !node.nodeId || !datasetId || !dataset || dataset.isFetchStructureDisabled}
    >
      <Data
        hub={hub}
        node={node}
        catalog={catalog}
        categoryPath={categoryPath}
        isAccessible={isAccessible}
        datasetId={datasetId}
        datasetTitle={datasetTitle}
        notes={notes}
        attachedFiles={attachedFiles}
        referenceMetadataUrl={referenceMetadataUrl}
        dataset={dataset}
        viewId={viewId}
      />
    </Call>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(DatasetDomain);