import React, {useEffect, useState} from 'react';
import {useTranslation} from "react-i18next";
import "./style.css";
import WebFont from 'webfontloader';
import {numberFormatter} from "../../utils/formatters";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import Select from "@material-ui/core/Select";
import Call from "../../hocs/call/index";
import {
  getDataIdxFromDimensionsArray,
  getDimensionFiltersValue,
  getDimensionValuesIndexesMap
} from "../../utils/jsonStat";
import {addSpinnerMessage, markSpinnerMessage} from "../../state/spinner/spinnerActions";
import {v4 as uuidv4} from "uuid";
import {compose} from "redux";
import {connect} from "react-redux";


const mapDispatchToProps = dispatch => ({
  addSpinner: (uuid, message) => dispatch(addSpinnerMessage(uuid, message)),
  markSpinner: (uuid, isError) => dispatch(markSpinnerMessage(uuid, isError))
});


function Map(props) {
  const {mapLayout, jsonStat, onGeometryFetch, geometries, geometryDetailLevels, classes, hideSpinner, addSpinner, markSpinner} = props;

  const {t} = useTranslation();

  const [currentDetailLevel, setCurrentDetailLevel] = useState(-1);

  const [filteredGeometries, setFilteredGeometries] = useState(null);

  const [spinnerUuid] = useState(uuidv4());

  // select default detail level
  useEffect(() => {

    if (!geometryDetailLevels) {
      return;
    }

    if (geometryDetailLevels && geometryDetailLevels?.length > 0) {
      const firstNotEmptyLevelIndex = geometryDetailLevels.findIndex(el => el);
      setCurrentDetailLevel(geometryDetailLevels[firstNotEmptyLevelIndex].level);
    }

    return () => {
      window.LMap.destroyMap("map-root")
    }
  }, [geometryDetailLevels]);


  // init the map with filtered geometries
  useEffect(() => {

    if (!filteredGeometries || filteredGeometries.length === 0) {
      return;
    }

    const mapTranslations = {
      configure: t("components.map.translation.configure"),
      opacity: t("components.map.translation.opacity"),
      from: t("components.map.translation.from"),
      to: t("components.map.translation.to"),
      cancel: t("components.map.translation.cancel"),
      apply: t("components.map.translation.apply"),
      classification: t("components.map.translation.classification"),
      equalInterval: t("components.map.translation.equalInterval"),
      naturalBreaks: t("components.map.translation.naturalBreaks"),
      quantile: t("components.map.translation.quantile"),
      numberOfClasses: t("components.map.translation.numberOfClasses"),
      startColor: t("components.map.translation.startColor"),
      endColor: t("components.map.translation.endColor"),
      preview: t("components.map.translation.preview")
    };

    const isGeometryData = false;

    if (!hideSpinner) {
      addSpinner(spinnerUuid, t("components.map.spinners.rendering"));
    }

    const layerOptions = {
      srid: "EPSG:4326",
      legendSettings: {
        paletteStartColor: "#eff3ff",
        paletteEndColor: "#2171b5",
        paletteCardinality: 9,
        pointStartRadius: 1,
        pointEndRadius: 9,
        pointRadiusCardinality: 9,
        opacity: 0.9,
        classificationMethod: "quantile"
      },
      onDataRender: function () {
        markSpinner(spinnerUuid);
      },
      borderColor: [128, 128, 128, 0.5],
      valueFormatter: numberFormatter,
      centerMarker:
        isGeometryData
          ? "data:image/png;base64,"
          : undefined,
      centerMarkerText: isGeometryData ? "test" : undefined,
      selectedCenterMarker:
        isGeometryData
          ? "data:image/png;base64,"
          : undefined
    };

    const mapOptions = {
      baseMap: "osm",
      baseMapToGray: true,
      onLoadingStart: function () {
        console.log("Map creation - start");
      },
      onLoadingFinish: function () {
        console.log("Map creation - done");
      },
      legendFontFamily: "Roboto"
    };

    WebFont.load({
      google: {
        families: ['Roboto:300,400,500,700']
      },
      active: () => {
        window.LMap.initMap("map-root", mapOptions, mapTranslations);
        window.LMap.addLayer("map-root", filteredGeometries, "Dataset", layerOptions);
      }
    });

    return () => {
      window.LMap.destroyMap("map-root")
    }
  }, [filteredGeometries, spinnerUuid, hideSpinner, addSpinner, markSpinner, t]);


  // check for missing geometry ids
  useEffect(() => {
    if (!geometries) {
      return;
    }
    const geometryMap = {};
    geometries.forEach(g => geometryMap[g.id] = g);

    const missingGeometryList = [];
    jsonStat.dimension[mapLayout.territoryDim].category.index.forEach(geomId => {
      if (!geometryMap[geomId]) {
        const missingEntry = {id: geomId, label: jsonStat.dimension[mapLayout.territoryDim].category.label[geomId]};
        missingGeometryList.push(missingEntry);
      }
    });
    console.log("missing ids", missingGeometryList);
  }, [jsonStat, geometries, mapLayout.territoryDim])


  useEffect(() => {

    if (!geometries || !currentDetailLevel < 0) {
      return;
    }

    const geometryMap = {};
    geometries.forEach(g => geometryMap[g.id] = g);

    const filters = [...mapLayout.filters];
    const filtersValues = {...mapLayout.filtersValue};
    const territoryDimName = mapLayout.territoryDim;
    const dimensionFilterValues = getDimensionFiltersValue(jsonStat, mapLayout.territoryDim, filters, filtersValues);
    const indexesMap = getDimensionValuesIndexesMap(jsonStat);

    const data = [];
    var levelsCounter = {};
    dimensionFilterValues.forEach(territoryDim => {
      const dimValueArray = jsonStat.id.map(dim => {
        if (dim === territoryDimName) {
          return territoryDim.id;
        } else {
          return filtersValues[dim];
        }
      });
      const valueIdx = getDataIdxFromDimensionsArray(
        dimValueArray.map((value, idx) => indexesMap[jsonStat.id[idx]][value]),
        jsonStat.size
      );
      const valueId = jsonStat.value[valueIdx];
      const geom = geometryMap[territoryDim.id];

      if (valueId && geom && geom.nutsLevel === +currentDetailLevel) {
        data.push({
          "value": valueId,
          "identifier": geom.uniqueId,
          "geometry": geom.wkt,
          "label": territoryDim.label
        });
      }
      if (valueId && geom?.nutsLevel >= 0) {
        if (!levelsCounter[geom.nutsLevel]) {
          levelsCounter[geom.nutsLevel] = 0;
        }
        levelsCounter[geom.nutsLevel]++;
      }
    });

    /*
    // set the first not empty level
    if(data.length === 0){
      const firstNotEmpty = Object.entries(levelsCounter).filter((entry) => {return entry[1] > 1;});
      if(firstNotEmpty?.length > 0){
        setCurrentDetailLevel(firstNotEmpty[0][0]);
        return;
      }
    }
    */

    setFilteredGeometries(data);

  }, [geometries, mapLayout, currentDetailLevel, jsonStat])


  return (
    jsonStat.dimension[mapLayout.territoryDim]
      ? (
        <Call
          cb={onGeometryFetch}
          cbParam={[...jsonStat.dimension[mapLayout.territoryDim].category.index]}
          disabled={geometries !== null && geometries !== undefined && geometryDetailLevels !== null && geometryDetailLevels !== undefined}
        >
          {geometryDetailLevels && geometryDetailLevels.length > 1 && (
            <FormControl className={classes.filters}>
              <InputLabel id="Map detail level label">{t("components.map.detailLevel")}</InputLabel>
              <Select
                value={currentDetailLevel}
                onChange={(ev) => {
                  setCurrentDetailLevel(ev.target.value)
                }}
              >
                {geometryDetailLevels.map(val =>
                  <MenuItem value={val.level} key={val.level}>{val.label}</MenuItem>
                )}
              </Select>
            </FormControl>
          )}
          {currentDetailLevel > 0 && filteredGeometries && filteredGeometries.length === 0 && (
            <span style={{textAlign: 'center', width: '100%'}}>
              <h2>
                {t("components.map.noDataToDisplayFor")}
                {" "}
                {(geometryDetailLevels || []).filter(lev => lev.level === +currentDetailLevel).map(val =>
                  <i key={"span-i-" + val.level}>{val.label}</i>
                )}
                .
              </h2>
            </span>
          )}
          <div id="map-root"/>
        </Call>
      )
      : <span style={{textAlign: 'center', width: '100%'}}><h2>{t("components.map.noTerritorialDim")}</h2></span>
  );
}

export default compose(
  connect(null, mapDispatchToProps)
)(Map);
