import withStyles from "@material-ui/core/styles/withStyles";
import {useTranslation} from "react-i18next";
import DataViewerHeader from "./Header";
import DataViewerSideBar from "./Sidebar";
import DataViewerFooter from "./Footer";
import DatasetFilters from "../dataset-filters";
import JsonStatTable, {
  JSONSTAT_TABLE_FONT_SIZE_LG,
  JSONSTAT_TABLE_FONT_SIZE_MD,
  JSONSTAT_TABLE_FONT_SIZE_SM
} from "../table";
import Criteria from "../criteria";
import TableLayout from "../table-layout";
import Dialog from "@material-ui/core/Dialog";
import Button from "@material-ui/core/Button";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import DialogTitle from "@material-ui/core/DialogTitle";
import Card from "@material-ui/core/Card";
import Grid from "@material-ui/core/Grid";
import FontSizeSelector, {
  FONT_SIZE_SELECTOR_FONT_SIZE_MD,
  FONT_SIZE_SELECTOR_FONT_SIZE_SM
} from "../font-size-selector";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import FullscreenIcon from '@material-ui/icons/Fullscreen';
import FullscreenExitIcon from '@material-ui/icons/FullscreenExit';
import "./style.css"
import CustomEmpty from "../custom-empty";
import TableChartIcon from "@material-ui/icons/TableChart";
import MapIcon from "@material-ui/icons/Map";
import BarChartIcon from "@material-ui/icons/BarChart";
import TimelineIcon from "@material-ui/icons/Timeline";
import HelpIcon from "@material-ui/icons/Help";
import DonutLargeIcon from "@material-ui/icons/DonutLarge";
import PieChartIcon from "@material-ui/icons/PieChart";
import {compose} from "redux";
import {connect} from "react-redux";
import {
  enableDatasetFetch,
  fetchAccessibleDataset,
  fetchDataset,
  fetchDatasetGeometries,
  hideDatasetCriteria,
  hideDatasetCriteriaAlert,
  hideDatasetCriteriaLengthWarning,
  hideDatasetDownloadWarning,
  hideDatasetUnavailableViewWarning,
  resetDataset,
  setDatasetLabelFormat,
  setDatasetViewer,
  showDatasetCriteria,
  submitDatasetDownload,
  submitDatasetViewTemplate
} from "../../state/dataset/datasetActions";
import Alert from "@material-ui/lab/Alert";
import {getChartDimensionValues, getInitialFiltersValue, getJsonStatTableSize} from "../../utils/jsonStat";
import {numberFormatter} from "../../utils/formatters";
import Call from "../../hocs/call";
import _ from "lodash"
import ChartWrapper from "./ChartWrapper";
import ChartLayout from "../chart-layout";
import {v4 as uuidv4} from 'uuid';
import Map from "../map/index";
import LabelFormatSelector, {LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME} from "../label-format-selector";

const SIDEBAR_WIDTH = 80;
const TABLE_ACTIONS_WIDTH = 336;

const TIME_PERIOD_KEY = "TIME_PERIOD";
const FREQ_KEY = "FREQ";

export const MAX_COL_COUNT = 10000;
export const MAX_ROW_COUNT = 10000;

const styles = theme => ({
  root: {
    position: "absolute",
    width: "100%"
  },
  header: {
    width: "100%"
  },
  page: {
    width: "100%"
  },
  sidebar: {
    display: "inline-block",
    verticalAlign: "top",
    width: SIDEBAR_WIDTH,
    height: "100%",
    overflow: "auto",
    marginLeft: theme.spacing(2),
    padding: "0 8px"
  },
  data: {
    display: "inline-block",
    verticalAlign: "top",
    width: `calc(100% - ${SIDEBAR_WIDTH + theme.spacing(2) + theme.spacing(2) + theme.spacing(2)}px)`,
    height: "100%",
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    padding: theme.spacing(2)
  },
  viewerContainer: {
    width: "100%",
    height: "100%"
  },
  action: {
    height: 40,
    "& button": {
      padding: 8
    }
  },
  filtersAndActions: {
    width: "100%",
    paddingBottom: theme.spacing(1)
  },
  filters: {
    width: `calc(100% - ${TABLE_ACTIONS_WIDTH}px)`,
    display: "inline-block"
  },
  actions: {
    width: TABLE_ACTIONS_WIDTH,
    display: "inline-block"
  },
  viewer: {
    width: "100%",
    position: "relative"
  },
  attributes: {
    width: "100%",
    paddingTop: 12
  },
  footer: {
    bottom: 0,
    left: 0,
    width: "100%",
    overflowX: "auto",
    overflowY: "hidden",
    padding: theme.spacing(1)
  },
  warningAlert: {
    width: "100%",
    marginBottom: 16
  },
  criteriaContent: {
    overflow: "hidden !important"
  }
});

const mapDispatchToProps = dispatch => ({
  fetchDataset: ({nodeId, datasetId, filters, datasetTitle, checkCriteriaLength, tableLayout}) => dispatch(fetchDataset(nodeId, datasetId, filters, datasetTitle, checkCriteriaLength, tableLayout)),
  fetchAccessibleDataset: ({nodeId, datasetId, filters, datasetTitle}) => dispatch(fetchAccessibleDataset(nodeId, datasetId, filters, datasetTitle)),
  onFetchDatasetEnable: () => dispatch(enableDatasetFetch()),
  onCriteriaShow: () => dispatch(showDatasetCriteria()),
  onCriteriaHide: () => dispatch(hideDatasetCriteria()),
  onCriteriaAlertHide: () => dispatch(hideDatasetCriteriaAlert()),
  onViewerChange: viewerIdx => dispatch(setDatasetViewer(viewerIdx)),
  onLabelFormatChange: labelFormat => dispatch(setDatasetLabelFormat(labelFormat)),
  onDownloadSubmit: (nodeId, datasetId, datasetTitle, format, extension) =>
    dispatch(submitDatasetDownload(nodeId, datasetId, datasetTitle, format, extension)),
  onDownloadHide: () => dispatch(hideDatasetDownloadWarning()),
  onViewTemplateSubmit: (nodeId, viewTemplate, isView) => dispatch(submitDatasetViewTemplate(nodeId, viewTemplate, isView)),
  onUnavailableViewHide: () => dispatch(hideDatasetUnavailableViewWarning()),
  onCriteriaLengthWarningHide: () => dispatch(hideDatasetCriteriaLengthWarning()),
  onGeometryFetch: (idList) => dispatch(fetchDatasetGeometries(idList)),
  onDatasetReset: () => dispatch(resetDataset())
});

const viewers = t => [
  {
    key: 0,
    type: "table",
    title: t ? t("scenes.dataViewers.viewers.table") : "",
    icon: <TableChartIcon/>
  },
  {
    key: 1,
    title: t ? t("scenes.dataViewers.viewers.map") : "",
    type: "map",
    icon: <MapIcon/>
  },
  {
    key: 2,
    title: t ? t("scenes.dataViewers.viewers.verticalBar") : "",
    type: "bar",
    icon: <BarChartIcon/>,
    chartType: "bar"
  },
  {
    key: 3,
    title: t ? t("scenes.dataViewers.viewers.horizontalBar") : "",
    type: "horizontalBar",
    icon: <BarChartIcon style={{transform: "rotate(90deg)"}}/>,
    chartType: "horizontalBar"
  },
  {
    key: 4,
    title: t ? t("scenes.dataViewers.viewers.line") : "",
    type: "line",
    icon: <TimelineIcon/>,
    chartType: "line"
  },
  {
    key: 5,
    title: t ? t("scenes.dataViewers.viewers.area") : "",
    type: "area",
    icon: <HelpIcon/>,
    chartType: "area"
  },
  {
    key: 6,
    title: t ? t("scenes.dataViewers.viewers.doughnut") : "",
    type: "doughnut",
    icon: <DonutLargeIcon/>,
    chartType: "doughnut"
  },
  {
    key: 7,
    title: t ? t("scenes.dataViewers.viewers.pie") : "",
    type: "pie",
    icon: <PieChartIcon/>,
    chartType: "pie"
  },
  {
    key: 8,
    title: t ? t("scenes.dataViewers.viewers.radar") : "",
    type: "radar",
    icon: <HelpIcon/>,
    chartType: "radar"
  },
  {
    key: 9,
    title: t ? t("scenes.dataViewers.viewers.polarArea") : "",
    type: "polarArea",
    icon: <HelpIcon/>,
    chartType: "polarArea"
  },
  {
    key: 10,
    title: t ? t("scenes.dataViewers.viewers.pyramid") : "",
    type: "pyramid",
    icon: <HelpIcon/>,
    chartType: "pyramid"
  }
];

export const getViewerIdxFromType = viewerType => {
  if (!viewerType) {
    return 0
  }

  const viewer = viewers().find(({type}) => type === viewerType);
  return viewer
    ? viewer.key
    : 0
}

const handleHeight = () => {
  const titleHeight = document.getElementById("node-header")
    ? document.getElementById("node-header").offsetHeight
    : 0;
  const headerHeight = document.getElementById("data-viewer__header")
    ? document.getElementById("data-viewer__header").offsetHeight
    : 0;
  const footerHeight = document.getElementById("data-viewer__footer")
    ? document.getElementById("data-viewer__footer").offsetHeight
    : 0;
  const filtersAndActionsHeight = document.getElementById("data-viewer__page__viewer__filters-and-actions")
    ? document.getElementById("data-viewer__page__viewer__filters-and-actions").offsetHeight
    : 0;

  if (document.getElementById("data-viewer")) {
    document.getElementById("data-viewer").setAttribute("style", `height: calc(100% - ${titleHeight}px); top: ${titleHeight}px`);
  }
  if (document.getElementById("data-viewer__page")) {
    document.getElementById("data-viewer__page").setAttribute("style", `height: calc(100% - ${headerHeight + footerHeight}px)`);
  }
  if (document.getElementById("data-viewer__page__viewer__viewer")) {
    document.getElementById("data-viewer__page__viewer__viewer").setAttribute("style", `height: calc(100% - ${filtersAndActionsHeight}px)`);
  }
};

const getInitialTableLayout = (jsonStat, territorialDims) => {
  if (!jsonStat) {
    return null;
  }

  let dimensions = [...jsonStat.id];

  let rows = dimensions.filter(dim => dim === TIME_PERIOD_KEY);
  dimensions = dimensions.filter(dim => !rows.includes(dim));

  let cols = dimensions.filter(dim => territorialDims.includes(dim));
  dimensions = dimensions.filter(dim => !cols.includes(dim));

  let filters = dimensions.filter(dim => jsonStat.size[jsonStat.id.indexOf(dim)] === 1);
  dimensions = dimensions.filter(dim => !filters.includes(dim));

  if (dimensions.includes(FREQ_KEY)) {
    rows = [FREQ_KEY, ...rows];
    dimensions = dimensions.filter(dim => !rows.includes(dim));
  }

  if (rows.length === 1 && jsonStat.size[jsonStat.id.indexOf(rows[0])] === 1 && cols.length === 1) {
    filters = filters.concat(rows);
    rows = [...cols];
    cols = [];
  }

  let colCount = 1;
  cols.forEach(col => colCount *= jsonStat.size[jsonStat.id.indexOf(col)]);
  let rowCount = 1;
  rows.forEach(row => rowCount *= jsonStat.size[jsonStat.id.indexOf(row)]);

  dimensions.forEach(dim => {
    const newColCount = colCount * jsonStat.size[jsonStat.id.indexOf(dim)];

    if (newColCount > MAX_COL_COUNT) {
      const newRowCount = rowCount * jsonStat.size[jsonStat.id.indexOf(dim)];

      if (newRowCount > MAX_ROW_COUNT) {
        filters.push(dim);

      } else {
        rows = [dim].concat(rows);
        rowCount = newRowCount;
      }

    } else {
      cols.push(dim);
      colCount = newColCount;
    }
  })

  const filtersValue = getInitialFiltersValue(jsonStat, filters);

  return {
    rows: rows,
    cols: cols,
    sections: [],
    filters: filters,
    filtersValue: filtersValue,
  }
};

const getInitialChartLayout = jsonStat => {
  if (!jsonStat) {
    return null
  }

  let dimensions = [...jsonStat.id];

  let primaryDim = dimensions.find(dim => dim === TIME_PERIOD_KEY);
  dimensions = dimensions.filter(dim => dim !== primaryDim);

  const filters = dimensions;
  const filtersValue = getInitialFiltersValue(jsonStat, dimensions);

  return {
    primaryDim: [primaryDim],
    primaryDimValues: getChartDimensionValues(jsonStat, primaryDim, filters, filtersValue),
    secondaryDim: [],
    secondaryDimValues: [],
    filters: filters,
    filtersValue: filtersValue,
  }
};

const getInitialMapLayout = jsonStat => {
  if (!jsonStat) {
    return null
  }

  let dimensions = [...jsonStat.id];
  let geoId = jsonStat.role?.geo?.length > 0 ? jsonStat.role.geo[0] : null;
  let territoryDim = dimensions.find(dim => (geoId !== null && dim === geoId) || dim === 'REF_AREA' || dim === 'ITTER107');

  const filters = dimensions.filter(dim => dim !== territoryDim);
  const filtersValue = getInitialFiltersValue(jsonStat, dimensions);

  return {
    territoryDim: territoryDim,
    filters: filters,
    filtersValue: filtersValue,
    labelFormat: LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME
  }
};

function DataViewer(props) {
  const {
    classes,

    nodeId,
    nodeCode,
    nodeExtras,

    isAccessible,

    datasetId,
    datasetTitle,
    notes,
    attachedFiles,
    referenceMetadataUrl,
    dataset,
    isFetchDatasetDisabled,

    isCriteriaVisible,
    isCriteriaAlertVisible,

    hasTemplateLayout,
    hasAnnotationLayout,

    dimensions,
    mode,
    type,
    codelistsLength,
    viewerIdx,
    initialTableLayout,
    initialMapLayout,
    initialChartLayout,
    labelFormat,
    filters,
    decimalSeparator,
    decimalPlaces,
    emptyChar,
    enableCriteria,
    enableLayout,
    maxAllowedCells,
    territorialDims,
    codes,
    isCodesFlat,
    codelists,
    areCodelistsFlat,
    timePeriod,

    isPartialData,
    isEmptyData,
    isTooBigData,

    maxObservation,

    isDownloadWarningVisible,
    isUnavailableViewWarningVisible,
    isCriteriaLengthWarningVisible,

    timings,
    geometries,
    geometryDetailLevels,

    fetchDataset,
    fetchAccessibleDataset,
    onFetchDatasetEnable,
    onCriteriaShow,
    onCriteriaHide,
    onCriteriaAlertHide,
    onViewerChange,
    onLabelFormatChange,
    onDownloadSubmit,
    onDownloadHide,
    onViewTemplateSubmit,
    onUnavailableViewHide,
    onGeometryFetch,
    onCriteriaLengthWarningHide,
    onDatasetReset
  } = props;

  const {t} = useTranslation();

  const [jsonStat, setJsonStat] = useState(null);

  const [tableLayout, setTableLayout] = useState(null);
  const [tmpTableLayout, setTmpTableLayout] = useState(null);

  const [chartLayout, setChartLayout] = useState(null);
  const [tmpChartLayout, setTmpChartLayout] = useState(null);

  const [mapLayout, setMapLayout] = useState(null);

  const [isLayoutVisible, setLayoutVisibility] = useState(false);

  const [isCriteriaValid, setCriteriaValidity] = useState(true);

  const [isFullscreen, setFullscreen] = useState(false);
  const [fontSize, setFontSize] = useState(FONT_SIZE_SELECTOR_FONT_SIZE_MD);

  useEffect(() => {
    return () => onDatasetReset()
  }, [onDatasetReset]);

  useEffect(() => {
    window.addEventListener("resize", handleHeight);
    return () => window.removeEventListener("resize", handleHeight)
  }, []);

  useEffect(() => {
    handleHeight();
  }, [jsonStat, viewerIdx, tableLayout]);

  useEffect(() => {
    setTableLayout(null);
    setTmpTableLayout(null);
    setChartLayout(null);
    setTmpChartLayout(null);
  }, [dataset]);

  useEffect(() => {
    setJsonStat(dataset);
    const cellCount = getJsonStatTableSize(dataset);

    if (cellCount < maxAllowedCells) {
      if (dataset) {

        const newTableLayout = initialTableLayout !== null
          ? initialTableLayout
          : getInitialTableLayout(dataset, territorialDims);
        setTableLayout(newTableLayout);
        setTmpTableLayout(newTableLayout);

        const newChartLayout = initialChartLayout !== null
          ? initialChartLayout
          : getInitialChartLayout(dataset);
        setChartLayout(newChartLayout);
        setTmpChartLayout(newChartLayout);

        const newMapLayout = initialMapLayout !== null
          ? initialMapLayout
          : getInitialMapLayout(dataset);
        setMapLayout(newMapLayout);
      }

    } else {
      setTableLayout(null);
      setTmpTableLayout(null);
      setChartLayout(null);
      setTmpChartLayout(null);
      setMapLayout(null);
    }

  }, [dataset, initialTableLayout, initialMapLayout, initialChartLayout, maxAllowedCells, territorialDims, isAccessible]);

  const handleSetViewer = viewerIdx => {
    onViewerChange(viewerIdx);
  };

  const handleSetData = dataIdx => {
    // fetch new data
  };

  const handleLayoutOpen = () => {
    if (jsonStat) {
      setLayoutVisibility(true);
    }
  };

  const handleLayoutClose = () => {
    setLayoutVisibility(false);
    setTmpTableLayout(tableLayout);
    setTmpChartLayout(chartLayout);
  };

  const handleLayoutSubmit = () => {
    setLayoutVisibility(false);

    if (viewerIdx === 0) {
      const newTableLayout = _.cloneDeep(tmpTableLayout);

      newTableLayout.filtersValue = getInitialFiltersValue(jsonStat, newTableLayout.filters);

      setTableLayout(newTableLayout);

    } else if (viewerIdx >= 2) {
      const newChartLayout = _.cloneDeep(tmpChartLayout);
      const filtersValue = getInitialFiltersValue(jsonStat, newChartLayout.filters);

      newChartLayout.filtersValue = filtersValue;

      newChartLayout.primaryDimValues = chartLayout.primaryDim[0] !== newChartLayout.primaryDim[0]
        ? getChartDimensionValues(jsonStat, newChartLayout.primaryDim[0], chartLayout.filters, filtersValue, 10)
        : chartLayout.primaryDimValues;

      newChartLayout.secondaryDimValues = chartLayout.secondaryDim[0] !== newChartLayout.secondaryDim[0]
        ? getChartDimensionValues(jsonStat, newChartLayout.secondaryDim[0], chartLayout.filters, filtersValue, 10)
        : chartLayout.secondaryDimValues;

      setChartLayout(newChartLayout);
    }
  };

  const handleCriteriaOpen = () => {
    onCriteriaShow();
  }

  const handleCriteriaClose = () => {
    onCriteriaHide();
    setCriteriaValidity(true);
  }

  const handleCriteriaSubmit = () => {
    onFetchDatasetEnable();
  }

  const handleFilterSelect = (dimension, value) => {

    const getUpdatedLayout = layout => {
      const newLayout = _.cloneDeep(layout);
      newLayout.filtersValue[dimension] = value;

      const filters = newLayout.filters.filter(filter => jsonStat.size[jsonStat.id.indexOf(filter)] > 1);
      const fixedFilters = filters.slice(0, filters.indexOf(dimension) + 1);
      const fixedFiltersValue = {};
      fixedFilters.forEach(filter => fixedFiltersValue[filter] = newLayout.filtersValue[filter]);

      newLayout.filtersValue = {
        ...newLayout.filtersValue,
        ...getInitialFiltersValue(jsonStat, filters.slice(filters.indexOf(dimension) + 1), fixedFiltersValue)
      };

      return newLayout
    };

    if (viewerIdx === 0) {
      setTableLayout(getUpdatedLayout(tableLayout))

    } else if (viewerIdx === 1) {
      setMapLayout(getUpdatedLayout(mapLayout))

    } else if (viewerIdx >= 2) {
      let newChartLayout = _.cloneDeep(chartLayout);

      if (chartLayout.filters.includes(dimension)) {
        newChartLayout = getUpdatedLayout(chartLayout);
      } else if (chartLayout.primaryDim.includes(dimension)) {
        newChartLayout.primaryDimValues = value;
      } else if (chartLayout.secondaryDim.includes(dimension)) {
        newChartLayout.secondaryDimValues = value;
      }

      setChartLayout(newChartLayout);
    }
  };

  const handleFontSizeSelect = size => {
    setFontSize(size);
  };

  const handleFullscreen = () => {
    !isFullscreen
      ? document.getElementById("data-viewer__page__viewer").classList.add("data-viewer__page__viewer--fullscreen")
      : document.getElementById("data-viewer__page__viewer").classList.remove("data-viewer__page__viewer--fullscreen");

    setFullscreen(!isFullscreen);
  };

  return (
    <div id="data-viewer" className={classes.root}>
      {false //isAccessible
        ? (
          <Call
            cb={fetchAccessibleDataset}
            cbParam={{nodeId, datasetId, filters, datasetTitle}}
            disabled={!nodeId || !datasetId || !filters || !datasetTitle || isFetchDatasetDisabled}
          >
            <span>accessible data</span>
          </Call>
        )
        : (
          <Fragment>
            <Call
              cb={fetchDataset}
              cbParam={{nodeId, datasetId, filters, datasetTitle, checkCriteriaLength: true, tableLayout}}
              disabled={!nodeId || !datasetId || !filters || !datasetTitle || isFetchDatasetDisabled}
            >
              <div id="data-viewer__header" className={classes.header}>
                <DataViewerHeader
                  nodeId={nodeId}
                  nodeCode={nodeCode}
                  nodeDownloadFormats={nodeExtras?.DownloadFormats}
                  datasetId={datasetId}
                  datasetTitle={datasetTitle}
                  hasTemplateLayout={hasTemplateLayout}
                  hasAnnotationLayout={hasAnnotationLayout}
                  notes={notes}
                  attachedFiles={attachedFiles}
                  jsonStat={jsonStat}
                  hiddenAttributes={nodeExtras?.HiddenAttributes}
                  tableLayout={tableLayout}
                  mapLayout={mapLayout}
                  chartLayout={chartLayout}
                  labelFormat={labelFormat}
                  criteria={filters}
                  decimalSeparator={decimalSeparator}
                  decimalPlaces={decimalPlaces}
                  emptyChar={emptyChar}
                  viewers={viewers(t)}
                  viewerIdx={viewerIdx}
                  onViewTemplateSubmit={(viewTemplate, isView) => onViewTemplateSubmit(nodeId, viewTemplate, isView)}
                  onDownloadSubmit={(format, extension) => onDownloadSubmit(nodeId, datasetId, datasetTitle, format, extension)}
                  timings={timings}
                />
              </div>
              <div id="data-viewer__page" className={classes.page}>
                <Card className={classes.sidebar}>
                  <DataViewerSideBar
                    viewers={viewers(t)}
                    selected={viewerIdx}
                    referenceMetadataUrl={referenceMetadataUrl}
                    onViewerSelect={handleSetViewer}
                    onCriteriaOpen={enableCriteria ? handleCriteriaOpen : null}
                    onLayoutOpen={enableLayout ? handleLayoutOpen : null}
                  />
                </Card>
                <Card className={classes.data}>
                  <div id="data-viewer__page__viewer" className={classes.viewerContainer}>
                    <div id="data-viewer__page__viewer__filters-and-actions" className={classes.filtersAndActions}>
                      {jsonStat && (
                        <Fragment>
                          {isPartialData && (
                            <Alert severity="warning" className={classes.warningAlert}>
                              {t("scenes.dataViewer.warnings.maxObservation.label", {maxObservation: maxObservation ? numberFormatter(maxObservation) : ""})}
                            </Alert>
                          )}
                          <div className={classes.filters}>
                            {viewerIdx === 0 && tableLayout && (
                              <DatasetFilters
                                key={uuidv4()}
                                jsonStat={jsonStat}
                                hiddenAttributes={nodeExtras?.HiddenAttributes}
                                layout={tableLayout}
                                labelFormat={labelFormat}
                                onSelect={handleFilterSelect}
                              />
                            )}
                            {viewerIdx === 1 && mapLayout && (
                              <DatasetFilters
                                key={uuidv4()}
                                jsonStat={jsonStat}
                                hiddenAttributes={nodeExtras?.HiddenAttributes}
                                layout={mapLayout}
                                labelFormat={labelFormat}
                                onSelect={handleFilterSelect}
                              />
                            )}
                            {viewerIdx >= 2 && chartLayout && (
                              <DatasetFilters
                                key={uuidv4()}
                                jsonStat={jsonStat}
                                hiddenAttributes={nodeExtras?.HiddenAttributes}
                                layout={chartLayout}
                                labelFormat={labelFormat}
                                onSelect={handleFilterSelect}
                                multiSelectDimensions={[chartLayout.primaryDim[0], chartLayout.secondaryDim[0]]}
                                multiSelectDimensionsValues={{
                                  [chartLayout.primaryDim[0]]: chartLayout.primaryDimValues,
                                  [chartLayout.secondaryDim[0]]: chartLayout.secondaryDimValues
                                }}
                              />
                            )}
                          </div>
                          <div className={classes.actions}>
                            <Grid container>
                              <Grid item className={classes.action}>
                                <LabelFormatSelector
                                  labelFormat={labelFormat}
                                  setLabelFormat={labelFormat => onLabelFormatChange(labelFormat)}
                                />
                              </Grid>
                              <Grid item className={classes.action}>
                                <FontSizeSelector
                                  fontSize={fontSize}
                                  setFontSize={handleFontSizeSelect}
                                />
                              </Grid>
                              <Grid item className={classes.action}>
                                <Tooltip
                                  title={isFullscreen
                                    ? t("scenes.dataViewer.fullscreen.exit")
                                    : t("scenes.dataViewer.fullscreen.enter")
                                  }
                                >
                                  <IconButton onClick={handleFullscreen}>
                                    {isFullscreen ? <FullscreenExitIcon/> : <FullscreenIcon/>}
                                  </IconButton>
                                </Tooltip>
                              </Grid>
                            </Grid>
                          </div>
                        </Fragment>
                      )}
                    </div>
                    <div id="data-viewer__page__viewer__viewer" className={classes.viewer}>
                      {(() => {
                        if (!jsonStat) {
                          return isEmptyData
                            ? <CustomEmpty text={t("scenes.dataViewer.errors.emptyData")}/>
                            : <CustomEmpty text={t("scenes.dataViewer.errors.applyCriteria")}/>
                        }
                        if (viewerIdx === 0) {
                          return (tableLayout && getJsonStatTableSize(jsonStat) < maxAllowedCells)
                            ? (
                              <JsonStatTable
                                jsonStat={jsonStat}
                                layout={tableLayout}
                                labelFormat={labelFormat}
                                fontSize={fontSize === FONT_SIZE_SELECTOR_FONT_SIZE_SM
                                  ? JSONSTAT_TABLE_FONT_SIZE_SM
                                  : fontSize === FONT_SIZE_SELECTOR_FONT_SIZE_MD
                                    ? JSONSTAT_TABLE_FONT_SIZE_MD
                                    : JSONSTAT_TABLE_FONT_SIZE_LG
                                }
                                isFullscreen={isFullscreen}
                                decimalSeparator={decimalSeparator}
                                decimalPlaces={decimalPlaces}
                                emptyChar={emptyChar}
                                hiddenAttributes={nodeExtras?.HiddenAttributes}
                              />
                            )
                            : (
                              <CustomEmpty
                                text={t("scenes.dataViewer.errors.tooBigData")}
                              />
                            )
                        } else if (viewerIdx === 1) {
                          return (
                            <Map
                              jsonStat={jsonStat}
                              mapLayout={mapLayout}
                              onGeometryFetch={onGeometryFetch}
                              classes={classes}
                              geometries={geometries}
                              geometryDetailLevels={geometryDetailLevels}
                            />
                          )
                        } else if (viewerIdx >= 2) {
                          return (
                            <ChartWrapper
                              jsonStat={jsonStat}
                              type={viewers(t)[viewerIdx].chartType}
                              layout={chartLayout}
                            />
                          )
                        }
                      })()}
                    </div>
                  </div>
                </Card>
              </div>
              <div id="data-viewer__footer" className={classes.footer}>
                <DataViewerFooter
                  labels={dataset ? [datasetTitle] : []}
                  dataIdx={0}
                  setData={handleSetData}
                />
              </div>
            </Call>

            <Dialog
              open={isCriteriaVisible}
              fullWidth
              maxWidth="md"
              onClose={handleCriteriaClose}
            >
              <DialogTitle>
                {t("scenes.dataViewer.dialogs.criteria.title")}
              </DialogTitle>
              <DialogContent className={classes.criteriaContent}>
                {dimensions && isCriteriaVisible && (
                  <Fragment>

                    <Criteria
                      nodeId={nodeId}
                      datasetId={datasetId}
                      dimensions={dimensions}
                      mode={mode}
                      type={type}
                      codes={codes}
                      isCodesFlat={isCodesFlat}
                      codelists={codelists}
                      areCodelistsFlat={areCodelistsFlat}
                      codelistsLength={codelistsLength}
                      filters={filters}
                      timePeriod={timePeriod}
                      isCriteriaValid={isCriteriaValid}
                      setCriteriaValidity={setCriteriaValidity}
                    />

                    <Dialog
                      open={isCriteriaAlertVisible}
                      fullWidth
                      maxWidth="sm"
                      onClose={onCriteriaAlertHide}
                    >
                      {(() => {
                        if (isEmptyData) {
                          return (
                            <DialogTitle>
                              {t("scenes.dataViewer.errors.emptyData")}
                            </DialogTitle>
                          )

                        } else if (isTooBigData) {
                          const count = getJsonStatTableSize(jsonStat);

                          return (
                            <Fragment>
                              <DialogTitle>
                                {t("scenes.dataViewer.dialogs.tooBigData.title")}
                              </DialogTitle>
                              <DialogContent>
                                {count !== 0 && (
                                  <div style={{width: "100%", textAlign: "end"}}>
                                    {t("scenes.dataViewer.dialogs.tooBigData.content.requestCells")}: <b>{numberFormatter(count)}</b>
                                  </div>
                                )}
                                <div style={{width: "100%", textAlign: "end"}}>
                                  {t("scenes.dataViewer.dialogs.tooBigData.content.maxCells")}: <b>{numberFormatter(maxAllowedCells)}</b>
                                </div>
                                <div style={{width: "100%", textAlign: "end", marginTop: 16}}>
                                  {t("scenes.dataViewer.dialogs.tooBigData.content.applyCriteria")}
                                </div>
                              </DialogContent>
                            </Fragment>
                          )

                        } else {
                          return (
                            <DialogTitle>
                              {t("scenes.dataViewer.errors.genericError")}
                            </DialogTitle>
                          )
                        }
                      })()}
                      <DialogActions>
                        <Button autoFocus onClick={onCriteriaAlertHide} color="primary">
                          {t("commons.confirm.confirm")}
                        </Button>
                      </DialogActions>
                    </Dialog>

                  </Fragment>
                )}
              </DialogContent>
              <DialogActions>
                <Button onClick={handleCriteriaClose}>
                  {t("commons.confirm.cancel")}
                </Button>
                <Button
                  autoFocus
                  color="primary"
                  onClick={handleCriteriaSubmit}
                  disabled={!isCriteriaValid}
                >
                  {t("commons.confirm.apply")}
                </Button>
              </DialogActions>
            </Dialog>

            <Dialog
              open={isLayoutVisible && viewerIdx === 0}
              fullWidth
              maxWidth="md"
              onClose={handleLayoutClose}
            >
              <DialogTitle>
                {t("scenes.dataViewer.dialogs.tableLayout.title")}
              </DialogTitle>
              <DialogContent>
                <TableLayout
                  jsonStat={jsonStat}
                  layout={tmpTableLayout}
                  labelFormat={labelFormat}
                  setLayout={setTmpTableLayout}
                />
              </DialogContent>
              <DialogActions>
                <Button onClick={handleLayoutClose}>
                  {t("commons.confirm.cancel")}
                </Button>
                <Button autoFocus onClick={handleLayoutSubmit} color="primary">
                  {t("commons.confirm.apply")}
                </Button>
              </DialogActions>
            </Dialog>

            <Dialog
              open={isLayoutVisible && viewerIdx >= 2}
              fullWidth
              maxWidth="md"
              onClose={handleLayoutClose}
            >
              <DialogTitle>
                {t("scenes.dataViewer.dialogs.chartLayout.title")}
              </DialogTitle>
              <DialogContent>
                <ChartLayout
                  jsonStat={jsonStat}
                  layout={tmpChartLayout}
                  setLayout={setTmpChartLayout}
                />
              </DialogContent>
              <DialogActions>
                <Button onClick={handleLayoutClose}>
                  {t("commons.confirm.cancel")}
                </Button>
                <Button autoFocus onClick={handleLayoutSubmit} color="primary">
                  {t("commons.confirm.apply")}
                </Button>
              </DialogActions>
            </Dialog>

            <Dialog
              open={isDownloadWarningVisible}
              maxWidth="md"
              onClose={onDownloadHide}
            >
              <DialogContent>
                {t("scenes.dataViewer.dialogs.downloadFormat.content")}
              </DialogContent>
              <DialogActions>
                <Button onClick={onDownloadHide}>
                  {t("commons.confirm.confirm")}
                </Button>
              </DialogActions>
            </Dialog>

            <Dialog
              open={isUnavailableViewWarningVisible}
              maxWidth="md"
              onClose={onUnavailableViewHide}
            >
              <DialogContent>
                {t("scenes.dataViewer.dialogs.unavailableView.content")}
              </DialogContent>
              <DialogActions>
                <Button onClick={onUnavailableViewHide}>
                  {t("commons.confirm.confirm")}
                </Button>
              </DialogActions>
            </Dialog>

            <Dialog
              open={isCriteriaLengthWarningVisible}
              maxWidth="md"
              onClose={onCriteriaLengthWarningHide}
            >
              <DialogContent>
                {t("scenes.dataViewer.dialogs.tooLongQuery.content")}
              </DialogContent>
              <DialogActions>
                <Button onClick={onCriteriaLengthWarningHide}>
                  {t("commons.confirm.cancel")}
                </Button>
                <Button
                  onClick={() => fetchDataset({
                    nodeId,
                    datasetId,
                    filters,
                    datasetTitle,
                    checkCriteriaLength: false,
                    tableLayout
                  })}
                >
                  {t("commons.confirm.confirm")}
                </Button>
              </DialogActions>
            </Dialog>
          </Fragment>
        )}
    </div>
  );
}

export default compose(
  withStyles(styles),
  connect(null, mapDispatchToProps)
)(DataViewer)