import _ from "lodash";
import {
  LABEL_FORMAT_SELECTOR_LABEL_FORMAT_BOTH,
  LABEL_FORMAT_SELECTOR_LABEL_FORMAT_ID,
  LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME
} from "../components/label-format-selector";

const TIME_PERIOD_KEY = "TIME_PERIOD";

const toUpperCaseFirst = string => (string !== null && string.length > 0)
  ? string[0].toUpperCase() + string.slice(1)
  : string;

export const getDimensionLabelFromJsonStat = (jsonStat, dimension, format) => {
  const name = toUpperCaseFirst(jsonStat.dimension[dimension].label);
  const id = dimension;

  return (name || id);
};

export const getDimensionValueLabelFromJsonStat = (jsonStat, dimension, value, format) => {
  const name = toUpperCaseFirst(jsonStat.dimension[dimension].category.label[value]);
  const id = value;

  if (dimension === TIME_PERIOD_KEY) {
    return name;
  }

  switch (format) {
    case LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME:
      return name;
    case LABEL_FORMAT_SELECTOR_LABEL_FORMAT_ID:
      return id;
    case LABEL_FORMAT_SELECTOR_LABEL_FORMAT_BOTH:
      return `[${id}] ${name}`;
    default:
      return name;
  }
};

export const getDataIdxFromDimensionsArray = (dimensions, size) => {
  let dataIdx = 0;
  let multiplier = 1;

  for (let i = 0; i < dimensions.length; i++) {
    multiplier *= (i > 0) ? size[dimensions.length - i] : 1;
    dataIdx += multiplier * dimensions[dimensions.length - i - 1];
  }

  return dataIdx;
};

export const getJsonStatTableSize = (jsonStat, layout) => {
  if (jsonStat) {
    let count = 1;

    if (!layout) {
      jsonStat.size.forEach(el => count *= el);

    } else {
      let colCount = 1;
      layout.cols.forEach(col => colCount *= jsonStat.size[jsonStat.id.indexOf(col)]);
      let rowCount = 1;
      layout.rows.forEach(row => rowCount *= jsonStat.size[jsonStat.id.indexOf(row)]);
      layout.sections.forEach(section => rowCount *= jsonStat.size[jsonStat.id.indexOf(section)]);
      count = colCount * rowCount;
    }

    return count;

  } else {
    return 0
  }
};

export const getCombinationArrays = arrays => {
  if (!arrays || arrays.length === 0) {
    return [];
  }

  const ret = [];
  const max = arrays.length - 1;

  function helper(arr, i) {
    for (let j = 0, l = arrays[i].length; j < l; j++) {
      const a = arr.slice(0); // clone arr
      a.push(arrays[i][j]);
      if (i === max) {
        ret.push(a);
      } else {
        helper(a, i + 1);
      }
    }
  }

  helper([], 0);
  return ret;
};

const getCombinationArray = (jsonStat, dimsArrays, test) => {
  if (!dimsArrays || dimsArrays.length === 0) {
    return [];
  }

  const max = dimsArrays.length - 1;
  let ret = null

  function helper(arr, i) {
    if (!ret) {
      for (let j = 0, l = dimsArrays[i].length; j < l; j++) {
        if (!ret) {
          const a = arr.slice(0); // clone arr
          a.push(dimsArrays[i][j]);
          if (i === max) {
            if (!test || test(a)) {
              ret = ret || a;
            }
          } else {
            helper(a, i + 1);
          }
        }
      }
    }
  }

  helper([], 0);
  return ret;
};

export const getInitialFiltersValue = (jsonStat, filters, fixedFiltersValue) => {
  if (!jsonStat.id || jsonStat.id.length === 0) {
    return null;
  }

  let dims = [];
  if (filters.includes(TIME_PERIOD_KEY)) {
    dims.push(TIME_PERIOD_KEY);
  }
  dims = _.uniq(dims.concat(filters));
  dims = _.uniq(dims.concat(jsonStat.id));

  const indexesMap = fixedFiltersValue
    ? getDimensionValuesIndexesMap(jsonStat)
    : null;

  const dimsArrays = dims.map(dim => (!fixedFiltersValue || !Object.keys(fixedFiltersValue).includes(dim))
    ? jsonStat.dimension[dim].category.index.map((_, idx) => idx)
    : [indexesMap[dim][fixedFiltersValue[dim]]]
  );
  dimsArrays[dims.indexOf(TIME_PERIOD_KEY)].reverse();

  const isValorized = (orderedDims, unorderedDims, combination) => {
    const dimCombination = orderedDims.map(dim => combination[unorderedDims.indexOf(dim)]);
    const dataIdx = getDataIdxFromDimensionsArray(dimCombination, jsonStat.size);
    const value = jsonStat.value[dataIdx];
    return (value !== null && value !== undefined);
  }
  const unorderedDimCombination = getCombinationArray(jsonStat, dimsArrays, combination => isValorized(jsonStat.id, dims, combination));
  const dimCombination = jsonStat.id.map(dim => unorderedDimCombination[dims.indexOf(dim)]);

  const filtersValue = {};
  jsonStat.id.forEach((dim, idx) => {
    if (filters.includes(dim)) {
      filtersValue[dim] = jsonStat.dimension[dim].category.index[dimCombination[idx]];
    }
  })

  return filtersValue;
};

export const getFilteredLayoutObject = (jsonStat, initialLayout) => {
  const layout = _.cloneDeep(initialLayout);
  const dimArraysKey = ["rows", "cols", "filters", "sections", "primaryDim", "secondaryDim"];

  for (let key in layout) {
    if (layout.hasOwnProperty(key)) {
      if (dimArraysKey.includes(key)) {
        layout[key] = (layout[key] || []).filter(dim => jsonStat.id.includes(dim));
      }
    }
  }

  return layout
};

export const getDimensionFiltersValue = (jsonStat, dimension, filters, filtersValue) => {
  const values = [];
  const addedValues = {};

  if (filters.filter(filter => jsonStat.size[jsonStat.id.indexOf(filter)] > 1)[0] === dimension) {
    jsonStat.dimension[dimension].category.index.forEach(dimValue => values.push({
      id: dimValue,
      label: jsonStat.dimension[dimension].category.label[dimValue]
    }));

    return values
  }

  const dimensionsNotInFilters = jsonStat.id.filter(dim => !filters.includes(dim));
  const dimensionsPostFilter = filters.slice(filters.indexOf(dimension) + 1);
  const dimensions = dimensionsNotInFilters.concat(dimensionsPostFilter)

  jsonStat.dimension[dimension].category.index.forEach(dimValue => {
    const arrays = jsonStat.id.map(dim => {
      if (dimension === dim) {
        return [dimValue]
      } else if (dimensions.includes(dim)) {
        return jsonStat.dimension[dim].category.index
      } else {
        return [filtersValue[dim]]
      }
    });

    const indexesMap = getDimensionValuesIndexesMap(jsonStat);

    const isValorized = combination => {
      const dataIndexArr = combination.map((combinationValue, idx) => indexesMap[jsonStat.id[idx]][combinationValue]);
      const observation = jsonStat.value[getDataIdxFromDimensionsArray(dataIndexArr, jsonStat.size)];
      return (observation !== null && observation !== undefined && !addedValues[dimValue])
    };

    const combinationArray = getCombinationArray(jsonStat, arrays, isValorized);
    if (combinationArray) {
      values.push({
        id: dimValue,
        label: jsonStat.dimension[dimension].category.label[dimValue]
      });
      addedValues[dimValue] = true;
    }
  });

  return values;
};

export const getChartDimensionValues = (jsonStat, dimension, filters, filtersValue, count) => {
  if (!dimension) {
    return [];
  }

  const values = getDimensionFiltersValue(jsonStat, dimension, filters, filtersValue);

  return values.map(({id}) => id).slice(0, count ? count : undefined);
};

export const getDimensionValuesIndexesMap = jsonStat => {
  const indexesMap = {};

  jsonStat.id.forEach(dim => {
    indexesMap[dim] = {};
    jsonStat.dimension[dim].category.index.forEach((dimValue, idx) => indexesMap[dim][dimValue] = idx);
  });

  return indexesMap;
};

export const getAttributeObjects = (attributeList, attributeObj, hiddenAttributes) => {
  const attributes = [];
  const ids = [];
  let hasAsterisk = false;

  try {
    (attributeList || []).forEach((attr, idx) => {
      if (attr !== null && !(hiddenAttributes || []).includes(attributeObj[idx].id)) {
        const valueId = attributeObj[idx].values[attr].id;
        const valueLabel = attributeObj[idx].values[attr].name;

        attributes.push({
          id: attributeObj[idx].id,
          label: attributeObj[idx].name,
          valueId: valueId,
          valueLabel: valueLabel
        });

        if (valueId !== valueLabel && valueId.length <= 5) {
          ids.push(valueId);
        } else {
          hasAsterisk = true;
        }
      }
    });

    if (hasAsterisk) {
      ids.push("(*)");
    }

    return (attributes.length > 0 && ids.length > 0)
      ? {
        attributes,
        ids
      }
      : null

  } catch (error) {
    return -1
  }


};

export const getDimensionAttributeMap = (jsonStat, hiddenAttributes, getIdsHtmlString) => {
  if (!jsonStat) {
    return null;
  }

  const series = (jsonStat?.extension?.attributes?.series || []);
  const seriesIndexes = (jsonStat?.extension?.attributes?.index?.series || []);
  const dimensionAttributesMap = {};

  jsonStat.id.forEach(dim => dimensionAttributesMap[dim] = {});
  if (series && series.length > 0 && seriesIndexes && seriesIndexes.length > 0) {
    seriesIndexes
      .filter(({coordinates}) => (coordinates || []).filter(c => c !== null).length === 1)
      .forEach(index => {
        const dimIdx = index.coordinates.findIndex(el => el !== null);
        const dim = jsonStat.id[dimIdx];
        const dimValueIdx = index.coordinates.find(el => el !== null);
        const dimValue = jsonStat.dimension[dim].category.index[dimValueIdx];

        const attributeObjects = getAttributeObjects(index.attributes, series, hiddenAttributes);
        if (attributeObjects !== null && attributeObjects !== -1) {
          dimensionAttributesMap[dim][dimValue] = {
            attributes: attributeObjects.attributes,
            ids: attributeObjects.ids,
            htmlString: getIdsHtmlString
              ? getIdsHtmlString(attributeObjects.ids, dim, dimValue)
              : null
          };
        }
      });
  }

  return dimensionAttributesMap;
};