import React, {Fragment, useEffect, useState} from 'react';
import {compose} from "redux";
import {connect} from "react-redux";
import withStyles from "@material-ui/core/styles/withStyles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Call from "../../hocs/call";
import {
  CRITERIA_SELECTION_MODE_ALL,
  CRITERIA_SELECTION_MODE_STEP_BY_STEP,
  CRITERIA_SELECTION_TYPE_DYNAMIC,
  fetchDatasetStructureCodelist,
  fetchDatasetStructureCodelists,
  setDatasetStructureCodelistFilters
} from "../../state/dataset/datasetActions";
import EnhancedTree from "../enhanced-tree";
import _ from "lodash"
import PlaylistAddCheckIcon from "@material-ui/icons/PlaylistAddCheck"
import {Tooltip} from "@material-ui/core";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import InfiniteScrollTable from "../infinite-scroll-table";
import TimePeriod from "./TimePeriod";
import Typography from '@material-ui/core/Typography';
import {withTranslation} from "react-i18next";

const TIME_PERIOD_KEY = "TIME_PERIOD";

const styles = theme => ({
  root: {},
  treeContainer: {
    width: "100%",
    height: 340,
    padding: 8
  },
  timePeriodContainer: {
    width: "100%",
    height: 380,
    padding: 8
  },
  treeActions: {
    marginBottom: 8
  },
  codelistInfo: {
    margin: "16px 8px 0 8px"
  }
});

const mapStateToProps = state => ({
  structure: state.dataset.structure
});

const mapDispatchToProps = dispatch => ({
  fetchCodelist: ({nodeId, datasetId, dimensionId, type, filters}) => dispatch(fetchDatasetStructureCodelist(nodeId, datasetId, dimensionId, type, filters)),
  fetchCodelists: ({nodeId, datasetId, type}) => dispatch(fetchDatasetStructureCodelists(nodeId, datasetId, type)),
  onSetFilters: filters => dispatch(setDatasetStructureCodelistFilters(filters))
});

function Criteria(props) {
  const {
    t,
    classes,
    nodeId,
    datasetId,
    dimensions,
    mode,
    type,
    codes,
    isCodesFlat,
    codelists,
    areCodelistsFlat,
    codelistsLength,
    filters,
    timePeriod,
    fetchCodelist,
    fetchCodelists,
    onSetFilters,
    isCriteriaValid,
    setCriteriaValidity
  } = props;

  const [tabId, setTabId] = useState(0);
  const [isCallDisabled, setIsCallDisabled] = useState(false);

  const [tmpFilters, setTmpFilters] = useState(null);
  const [tmpTabId, setTmpTabId] = useState(null);

  const handleTabChange = tabId => {

    if (type === CRITERIA_SELECTION_TYPE_DYNAMIC && tabId < (dimensions.length - 1)) {
      const filtersToRemove = [];
      const newFilters = _.cloneDeep(filters);

      for (let i = tabId + 1; i < dimensions.length; i++) {
        if (filters[dimensions[i].id] !== null && filters[dimensions[i].id] !== undefined && filters[dimensions[i].id] !== []) {
          filtersToRemove.push(dimensions[i].id)
        }
      }

      if (filtersToRemove.length > 0) {
        filtersToRemove.forEach(dim => newFilters[dim] = undefined);
        setTmpFilters(newFilters);
        setTmpTabId(tabId);

      } else {
        setTabId(tabId);
        setIsCallDisabled(false);
      }

    } else {
      setTabId(tabId);
      setIsCallDisabled(false);
    }
  };

  const handleCheck = checkedKeys => {
    const newFilters = _.cloneDeep(filters);

    newFilters[dimensions[tabId].id] = checkedKeys;
    onSetFilters(newFilters);
  };

  useEffect(() => {
    if (dimensions && isCallDisabled === false) {
      setIsCallDisabled(true)
    }
  }, [dimensions, isCallDisabled]);

  const checkedKeys = (filters[dimensions[tabId].id] || []);

  return (
    <div className={classes.root}>
      {dimensions && (
        <Fragment>
          <AppBar position="static" color="default">
            <Tabs
              value={tabId}
              variant="scrollable"
              scrollButtons="auto"
              indicatorColor="primary"
              textColor="primary"
              onChange={(event, newValue) => handleTabChange(newValue)}
            >
              {(dimensions || []).map((dim, idx) => (
                <Tab
                  key={idx}
                  label={
                    <Tooltip title={dim.label || ""}>
                      <span>
                        {dim.id + (
                          codelistsLength[idx]
                            ? ` (${(filters && filters[dim.id]) ? filters[dim.id].length : 0}/${codelistsLength[idx]})`
                            : '')
                        }
                      </span>
                    </Tooltip>
                  }
                  disabled={!isCriteriaValid}
                />
              ))}
            </Tabs>
          </AppBar>
          <div key={tabId}>
            <Call
              cb={mode === CRITERIA_SELECTION_MODE_STEP_BY_STEP
                ? fetchCodelist
                : fetchCodelists
              }
              cbParam={{
                nodeId,
                datasetId,
                dimensionId: dimensions[tabId].id,
                mode,
                type,
                filters
              }}
              disabled={!nodeId || !datasetId || !dimensions || (mode === CRITERIA_SELECTION_MODE_ALL && !!codelists) || isCallDisabled}
            >
              {dimensions[tabId].id !== TIME_PERIOD_KEY
                ? (
                  <Fragment>
                    <Typography className={classes.codelistInfo}>
                      {`${dimensions[tabId].label || dimensions[tabId].id}${dimensions[tabId]?.extra?.DataStructureRef ? ` (${dimensions[tabId].extra.DataStructureRef})` : ""}`}
                    </Typography>
                    <div className={classes.treeContainer}>
                      {(isCodesFlat === true || (areCodelistsFlat && areCodelistsFlat[tabId] === true))
                        ? (
                          <InfiniteScrollTable
                            data={mode === CRITERIA_SELECTION_MODE_STEP_BY_STEP
                              ? (codes || [])
                              : ((codelists || {})[tabId] || [])
                            }
                            getRowKey={({id}) => id}
                            columns={[
                              {
                                title: t("components.criteria.table.columns.name.title"),
                                dataIndex: 'name',
                                render: (_, {id, name}) => `[${id}] ${name}`,
                                minWidthToContent: true,
                                noFilter: true
                              }
                            ]}
                            rowSelection={{
                              selectedRowKeys: checkedKeys,
                              onChange: handleCheck,
                            }}
                            height={200}
                          />
                        )
                        : (
                          <EnhancedTree
                            key={tabId}
                            tree={mode === CRITERIA_SELECTION_MODE_STEP_BY_STEP
                              ? (codes || [])
                              : ((codelists || {})[tabId] || [])
                            }
                            getNodeKey={({id}) => id}
                            childrenKey="children"
                            labelKey="name"
                            getNodeLabel={node => `[${node.id}] ${node.name}`}
                            isLabelNotMultilingual
                            checkable
                            withLevelSelector
                            checkedKeys={checkedKeys}
                            getIsCheckable={node => node.isSelectable !== false}
                            // defaultExpandedKeys={filters[dimensions[tabId].id] || []}
                            onCheck={handleCheck}
                            nodeActions={[
                              node => (node.children && node.children.length > 0)
                                ? {
                                  icon: <PlaylistAddCheckIcon/>,
                                  title: t("components.criteria.tree.nodeActions.selectAll.title"),
                                  onClick: node => {
                                    let newCheckedKeys = [...checkedKeys];
                                    const childrenKeys = (node.children || []).filter(({isSelectable}) => isSelectable !== false).map(({id}) => id)
                                    const unselectedChildrenKeys = childrenKeys.filter(key => !newCheckedKeys.includes(key))

                                    newCheckedKeys = (unselectedChildrenKeys.length > 0)
                                      ? newCheckedKeys.concat(childrenKeys)
                                      : newCheckedKeys.filter(key => !childrenKeys.includes(key));
                                    handleCheck(_.uniq(newCheckedKeys));
                                  }
                                }
                                : null
                            ]}
                          />
                        )}
                    </div>
                  </Fragment>
                )
                : (
                  <div className={classes.timePeriodContainer}>
                    <TimePeriod
                      timePeriod={timePeriod}
                      timePeriodKey={TIME_PERIOD_KEY}
                      filters={filters}
                      onSetFilters={onSetFilters}
                      isCriteriaValid={isCriteriaValid}
                      setCriteriaValidity={setCriteriaValidity}
                    />
                  </div>
                )
              }
            </Call>
          </div>

          <Dialog
            open={tmpFilters !== null}
            fullWidth
            maxWidth="sm"
            onClose={() => setTmpFilters(null)}
          >
            <DialogContent>
              {t("components.criteria.dialogs.warning.losingFilters.title")}
            </DialogContent>
            <DialogActions>
              <Button
                onClick={() => {
                  setTmpFilters(null);
                  setTmpTabId(null);
                }}
              >
                cancel
              </Button>
              <Button
                autoFocus
                onClick={() => {
                  setTmpFilters(null);
                  onSetFilters(tmpFilters);
                  setTmpTabId(null);
                  setTabId(tmpTabId);
                  setIsCallDisabled(false);
                }}
                color="primary"
              >
                ok
              </Button>
            </DialogActions>
          </Dialog>

        </Fragment>
      )}
    </div>
  )
}

export default compose(
  withStyles(styles),
  withTranslation(),
  connect(mapStateToProps, mapDispatchToProps)
)(Criteria)