import {Reducer} from "redux";
import {INIT} from '../rootActions';

export type AppConfigState = {
  baseURL: string
} | null;

const appConfigReducer: Reducer<AppConfigState> = (
    state = null,
    action
) => {
  switch (action.type) {
    case INIT: {
      return {
        baseURL: action.payload.baseURL
      };
    }
    default:
      return state;
  }
};

export default appConfigReducer;