import React, {Fragment} from 'react';
import CKEditor from 'ckeditor4-react';
import './style.css'
import {withStyles} from "@material-ui/core";

const styles = theme => ({
  preToolbar: {
    marginBottom: theme.spacing(1)
  }
});

class HtmlEditor extends React.Component {

  constructor(props) {
    super(props);
    this.state = HtmlEditor.getInitState(props);
    this.handleEditorChange = this.handleEditorChange.bind(this);
  }

  static getInitState(props) {
    return {
      value: props ? (props.value || '') : ''
    };
  }

  static getDerivedStateFromProps(props) {
    if (props.value !== null && props.value !== undefined) {
      return {
        value: props.value
      };

    } else {
      return HtmlEditor.getInitState(props);
    }
  }

  handleEditorChange(event) {
    const data = event.editor.getData();
    if (this.props.onChange) {
      this.props.onChange(data);
    }
  }

  render() {

    const {
      value
    } = this.state;

    const {
      disabled,
    } = this.props;

    return (
      <Fragment>
        {/* <Grid container className={classes.preToolbar}>
          <Grid item>
            <I18nInputAdornmentSelect/>
          </Grid>
        </Grid>*/}
        <CKEditor
          data={value}
          config={{
            defaultLanguage: 'en',
            language: 'en',
            toolbar: [
              {name: 'basicstyles', items: ['Bold', 'Italic', 'Underline', 'Strike']},
              {name: 'format', items: ['RemoveFormat']},
              {name: 'colors', items: ['TextColor', 'BGColor']},
              {name: 'insert', items: ['Table']},
              {name: 'links', items: ['Link', 'Unlink']},
              {name: 'paragraph', items: ['NumberedList', 'BulletedList']},
              {name: 'indent', items: ['Outdent', 'Indent']},
              {
                name: 'justify',
                items: ['JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock']
              },
              {name: 'clipboard', items: ['Cut', 'Copy', 'Paste']},
              {name: 'history', items: ['Undo', 'Redo']}
            ]
          }}
          readOnly={disabled ? disabled : false}
          onChange={this.handleEditorChange}
          onBeforeLoad={(CKEDITOR) => (CKEDITOR.disableAutoInline = true)} // needed to fix "Error code: editor-element-conflict."
        />
      </Fragment>
    );
  }
}

export default withStyles(styles)(HtmlEditor);
