import React, {Fragment, useEffect, useState} from "react";
import {connect} from "react-redux";
import {compose} from "redux";
import withStyles from "@material-ui/core/styles/withStyles";
import {
  DASHBOARD_ELEM_ENABLE_FILTERS_KEY,
  DASHBOARD_ELEM_FILTER_DIMENSION_KEY,
  DASHBOARD_ELEM_SHOW_TITLE_KEY,
  DASHBOARD_ELEM_TYPE_VALUE_TEXT,
  DASHBOARD_ELEM_TYPE_VALUE_VIEW,
  getViewIdxFromRowAndCol
} from "../../utils/dashboards";
import JsonStatTable from "../table";
import CustomEmpty from "../custom-empty";
import _ from "lodash";
import {getViewerIdxFromType} from "../data-viewer";
import ChartWrapper from "../data-viewer/ChartWrapper";
import DatasetFilters from "../dataset-filters";
import Map from "../map";
import {
  getDimensionLabelFromJsonStat,
  getDimensionValueLabelFromJsonStat,
  getFilteredLayoutObject
} from "../../utils/jsonStat";
import {useTranslation} from "react-i18next";
import {localizeI18nObj} from "../../utils/i18n";
import {
  DASHBOARD_VIEW_STATE_APPLY_FILTERS,
  DASHBOARD_VIEW_STATE_ERROR,
  DASHBOARD_VIEW_STATE_FETCHING
} from "../../state/dashboard/dashboardReducer";
import CircularProgress from "@material-ui/core/CircularProgress";
import "./style.css"
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import FullscreenIcon from '@material-ui/icons/Fullscreen';
import FullscreenExitIcon from '@material-ui/icons/FullscreenExit';
import {v4 as uuidv4} from 'uuid';

const styles = theme => ({
  row: {
    width: "100%",
    marginTop: 8
  },
  col: {
    display: "inline-block",
    verticalAlign: "top"
  },
  viewContainer: {
    padding: 16,
    height: 640,
    width: "100%",
    overflow: "auto"
  },
  viewPlaceholder: {
    height: "100%"
  },
  viewControllers: {
    paddingBottom: 16
  },
  viewTitle: {
    fontSize: 18,
    marginBottom: 8
  },
  viewStaticFilters: {
    marginBottom: 8
  },
  viewStaticFilter: {
    display: "inline-block",
    marginRight: 8
  },
  viewActiveFilters: {},
  view: {
    width: "100%"
  }
});

const handleHeight = (uuid, viewIdx) => {
  const controllersHeight = document.getElementById(`dashboard__view-container__${uuid}__controllers__${viewIdx}`)?.offsetHeight || 0;
  const view = document.getElementById(`dashboard__view-container__${uuid}__view__${viewIdx}`);
  if (view) {
    view.setAttribute("style", `height: calc(100% - ${controllersHeight}px)`);
  }
};

function DashboardCol(props) {

  const {
    t,
    classes,
    defaultLanguage,
    languages,
    dashboard,
    filterValue,
    rowIdx,
    colIdx,
    col,
    jsonStats,
    viewLayouts,
    onSelect,
    maps,
    fetchMapGeometries
  } = props;

  const [uuid] = useState(uuidv4());

  const [isFullscreen, setFullscreen] = useState(false);

  useEffect(() => {
    const func = () => handleHeight(uuid, getViewIdxFromRowAndCol(rowIdx, colIdx));

    window.addEventListener("resize", func);
    return () => window.removeEventListener("resize", func)
  }, [uuid, rowIdx, colIdx]);

  useEffect(() => {
    handleHeight(uuid, getViewIdxFromRowAndCol(rowIdx, colIdx));
  }, [uuid, rowIdx, colIdx, col, jsonStats, viewLayouts]);

  const handleFullscreen = () => {
    !isFullscreen
      ? document.getElementById(`dashboard__view-container__${uuid}__${getViewIdxFromRowAndCol(rowIdx, colIdx)}`).classList.add("dashboard__view-container--fullscreen")
      : document.getElementById(`dashboard__view-container__${uuid}__${getViewIdxFromRowAndCol(rowIdx, colIdx)}`).classList.remove("dashboard__view-container--fullscreen");

    setFullscreen(!isFullscreen);
  };

  return (
    <div className={classes.col} style={{width: `${col.widthPercentage}%`}}>
      <div
        id={`dashboard__view-container__${uuid}__${getViewIdxFromRowAndCol(rowIdx, colIdx)}`}
        className={classes.viewContainer}
      >
        {(() => {
          if (col.type === DASHBOARD_ELEM_TYPE_VALUE_TEXT) {
            return (
              <Fragment>
                <div style={{float: "right"}}>
                  <Tooltip
                    title={isFullscreen
                      ? t("components.dashboard.fullscreen.exit")
                      : t("components.dashboard.fullscreen.enter")
                    }
                  >
                    <IconButton
                      color="primary"
                      onClick={handleFullscreen}
                    >
                      {isFullscreen ? <FullscreenExitIcon/> : <FullscreenIcon/>}
                    </IconButton>
                  </Tooltip>
                </div>
                <div dangerouslySetInnerHTML={{__html: localizeI18nObj(col.value, defaultLanguage, languages)}}/>
              </Fragment>
            )
          } else if (jsonStats && viewLayouts) {
            return (
              <Fragment>
                {(() => {
                  const viewIdx = getViewIdxFromRowAndCol(rowIdx, colIdx);
                  const jsonStat = jsonStats[viewIdx];
                  let layout = _.cloneDeep(viewLayouts[viewIdx]);

                  if (jsonStat === DASHBOARD_VIEW_STATE_ERROR) {
                    return (
                      <div className={classes.viewPlaceholder}>
                        <CustomEmpty
                          text={t("components.dashboard.fetchingDatasetError")}
                          backgroundColor={"#f5f5f5"}
                        />
                      </div>
                    )
                  } else if (jsonStat === DASHBOARD_VIEW_STATE_APPLY_FILTERS) {
                    return (
                      <div className={classes.viewPlaceholder}>
                        <CustomEmpty
                          text={t("components.dashboard.applyFilters")}
                          backgroundColor={"#f5f5f5"}
                        />
                      </div>
                    )
                  } else if (jsonStat === DASHBOARD_VIEW_STATE_FETCHING) {
                    return (
                      <div className={classes.viewPlaceholder}>
                        <CustomEmpty
                          text={t("components.dashboard.fetching") + "..."}
                          backgroundColor={"#f5f5f5"}
                          image={<CircularProgress/>}
                        />
                      </div>
                    )
                  } else if (jsonStat === "") {
                    return (
                      <div className={classes.viewPlaceholder}>
                        <CustomEmpty
                          text={t("components.dashboard.emptyView")}
                          backgroundColor={"#f5f5f5"}
                        />
                      </div>
                    )
                  } else if (jsonStat && layout) {

                    const view = _.cloneDeep(dashboard.views[col.value]);
                    const viewerIdx = getViewerIdxFromType(view.defaultView);

                    const filter = col[DASHBOARD_ELEM_FILTER_DIMENSION_KEY];
                    if (filter && filterValue) {
                      if ((layout.filters || []).includes(filter)) {
                        layout.filtersValue = {
                          ...layout.filtersValue,
                          [filter]: filterValue
                        };
                      } else if ((layout.primaryDim || []).includes(filter)) {
                        layout.primaryDimValues = {
                          ...layout.primaryDimValues,
                          [filter]: filterValue
                        };
                      } else if ((layout.secondaryDim || []).includes(filter)) {
                        layout.secondaryDimValues = {
                          ...layout.secondaryDimValues,
                          [filter]: filterValue
                        };
                      }
                    }

                    const staticFilters = [];
                    layout.filters.forEach(dim => {
                      if (jsonStat.size[jsonStat.id.indexOf(dim)] === 1) {
                        staticFilters.push({
                          dim: dim,
                          dimLabel: getDimensionLabelFromJsonStat(jsonStat, dim),
                          value: jsonStat.dimension[dim].category.index[0],
                          valueLabel: getDimensionValueLabelFromJsonStat(jsonStat, dim, jsonStat.dimension[dim].category.index[0])
                        })
                      }
                    });
                    if (!col[DASHBOARD_ELEM_ENABLE_FILTERS_KEY]) {
                      layout.filters.forEach(dim => {
                        if (jsonStat.size[jsonStat.id.indexOf(dim)] !== 1) {
                          staticFilters.push({
                            dim: dim,
                            dimLabel: getDimensionLabelFromJsonStat(jsonStat, dim),
                            value: layout.filtersValue[dim],
                            valueLabel: getDimensionValueLabelFromJsonStat(jsonStat, dim, layout.filtersValue[dim])
                          })
                        }
                      });
                    }

                    return (
                      <Fragment>
                        <div
                          id={`dashboard__view-container__${uuid}__controllers__${getViewIdxFromRowAndCol(rowIdx, colIdx)}`}
                          className={classes.viewControllers}
                        >
                          <div style={{float: "right"}}>
                            <Tooltip
                              title={isFullscreen
                                ? t("components.dashboard.fullscreen.exit")
                                : t("components.dashboard.fullscreen.enter")
                              }
                            >
                              <IconButton
                                color="primary"
                                onClick={handleFullscreen}
                              >
                                {isFullscreen ? <FullscreenExitIcon/> : <FullscreenIcon/>}
                              </IconButton>
                            </Tooltip>
                          </div>
                          {col[DASHBOARD_ELEM_SHOW_TITLE_KEY] && (
                            <div className={classes.viewTitle}>
                              {localizeI18nObj(view.title, defaultLanguage, languages)}
                            </div>
                          )}
                          <div className={classes.viewStaticFilters}>
                            {staticFilters.map(({dim, dimLabel, value, valueLabel}, idx) =>
                              <div key={idx} className={classes.viewStaticFilter}>
                                <b>{(dimLabel || dim)}</b>: <i>{(valueLabel || value)}</i>
                              </div>
                            )}
                          </div>
                          {col[DASHBOARD_ELEM_ENABLE_FILTERS_KEY] && (
                            <div className={classes.viewActiveFilters}>
                              <DatasetFilters
                                jsonStat={jsonStat}
                                layout={layout}
                                onSelect={(dimension, value) => onSelect(viewIdx, dimension, value)}
                              />
                            </div>
                          )}
                        </div>
                        <div
                          id={`dashboard__view-container__${uuid}__view__${getViewIdxFromRowAndCol(rowIdx, colIdx)}`}
                          className={classes.view}
                        >
                          {(() => {
                            if (viewerIdx === 0) {
                              return (
                                <JsonStatTable
                                  jsonStat={jsonStat}
                                  layout={layout}
                                  labelFormat={view.layouts.labelFormat}
                                  decimalSeparator={","} // TODO
                                  decimalPlaces={view.decimalNumber} // TODO
                                  emptyChar={""} // TODO
                                  hideSpinner
                                />
                              )
                            } else if (viewerIdx === 1) {
                              return (
                                <Map
                                  classes={classes}
                                  jsonStat={jsonStat}
                                  mapLayout={layout}
                                  onGeometryFetch={idList => fetchMapGeometries(idList, viewIdx)}
                                  geometries={maps?.[viewIdx]?.geometries || null}
                                  geometryDetailLevels={maps?.[viewIdx]?.geometryDetailLevels || null}
                                  hideSpinner
                                />
                              )
                            } else {
                              return (
                                <ChartWrapper
                                  jsonStat={jsonStat}
                                  type={view.defaultView}
                                  layout={layout}
                                />
                              )
                            }
                          })()}
                        </div>
                      </Fragment>
                    )
                  } else {
                    return <span/>
                  }
                })()}
              </Fragment>
            )
          }
        })()}
      </div>
    </div>
  )
}

const DashboardRow = ({
                        t,
                        classes,
                        defaultLanguage,
                        languages,
                        dashboard,
                        filterValue,
                        row,
                        rowIdx,
                        jsonStats,
                        viewLayouts,
                        onSelect,
                        maps,
                        fetchMapGeometries
                      }) =>
  <div className={classes.row}>
    {row.map((col, idx) =>
      <DashboardCol
        key={idx}
        t={t}
        classes={classes}
        defaultLanguage={defaultLanguage}
        languages={languages}
        dashboard={dashboard}
        filterValue={filterValue}
        rowIdx={rowIdx}
        colIdx={idx}
        col={col}
        jsonStats={jsonStats}
        viewLayouts={viewLayouts}
        onSelect={onSelect}
        maps={maps}
        fetchMapGeometries={fetchMapGeometries}
      />
    )}
  </div>;

function Dashboard(props) {
  const {
    classes,
    defaultLanguage,
    languages,
    dashboard,
    filterValue,
    jsonStats,
    maps,
    fetchMapGeometries
  } = props;

  const {t} = useTranslation();

  const [viewLayouts, setViewLayouts] = useState(null);

  useEffect(() => {
    dashboard.dashboardConfig.forEach((row, rowIdx) => {
      row.forEach((col, colIdx) => {
        if (col.type === DASHBOARD_ELEM_TYPE_VALUE_VIEW) {
          setViewLayouts(prevViewLayouts => {
            const newViewLayouts = prevViewLayouts
              ? _.cloneDeep(prevViewLayouts)
              : {};

            const viewIdx = getViewIdxFromRowAndCol(rowIdx, colIdx);
            if (!jsonStats?.[viewIdx]?.id) {
              return newViewLayouts;
            }

            const layouts = JSON.parse(dashboard.views[col.value].layouts);
            let layout = (layouts.tableLayout || layouts.mapLayout || layouts.chartLayout);
            layout = getFilteredLayoutObject(jsonStats[viewIdx], layout);

            newViewLayouts[viewIdx] = layout;
            return newViewLayouts;
          });
        }
      })
    })
  }, [dashboard, jsonStats]);

  const handleFilterSelect = (viewIdx, dimension, value) => {
    setViewLayouts(prevViewLayouts => {
      const newViewLayouts = _.cloneDeep(prevViewLayouts);

      if (newViewLayouts[viewIdx].filters.includes(dimension)) {
        newViewLayouts[viewIdx].filtersValue[dimension] = value;
      } else if (newViewLayouts[viewIdx].primaryDim.includes(dimension)) {
        newViewLayouts[viewIdx].primaryDimValues = value;
      } else if (newViewLayouts[viewIdx].secondaryDim.includes(dimension)) {
        newViewLayouts[viewIdx].secondaryDimValues = value;
      }

      return newViewLayouts;
    });
  };

  return (
    <Fragment>
      {(dashboard.dashboardConfig || []).map((row, idx) =>
        <DashboardRow
          key={idx}
          t={t}
          classes={classes}
          defaultLanguage={defaultLanguage}
          languages={languages}
          dashboard={dashboard}
          filterValue={filterValue}
          row={row}
          rowIdx={idx}
          jsonStats={jsonStats}
          viewLayouts={viewLayouts}
          onSelect={handleFilterSelect}
          maps={maps}
          fetchMapGeometries={fetchMapGeometries}
        />
      )}
    </Fragment>
  )
}

export default compose(
  withStyles(styles),
  connect(state => ({
    languages: state.app.languages,
    defaultLanguage: state.app.language
  }))
)(Dashboard);