import React, {Component, createRef} from 'react';
import ChartJs from 'chart.js';
import {numberFormatter} from "../../utils/formatters";
import {
  getDataIdxFromDimensionsArray,
  getDimensionValueLabelFromJsonStat,
  getDimensionValuesIndexesMap
} from "../../utils/jsonStat";

const colors = [
  '#1E88E5',
  '#C2185B',
  '#FDD835',
  '#0E9D59',
  '#F06292',
  '#FF7043',
  '#8BC34A',
  '#00BCD4',
  '#EA3F4D',
  '#AA47BC',
  '#26A69A',
  '#FF9800',
];

const hexToRGBA = (hex, alpha) => {
  const r = parseInt(hex.slice(1, 3), 16);
  const g = parseInt(hex.slice(3, 5), 16);
  const b = parseInt(hex.slice(5, 7), 16);

  return "rgba(" + r + ", " + g + ", " + b + ", " + alpha + ")";
};

const getOptions = chartType => {

  const isBarOrLineGraph =
    chartType === "bar" ||
    chartType === "horizontalBar" ||
    chartType === "line" ||
    chartType === "area";

  return {
    responsive: true,
    maintainAspectRatio: false,
    scales: isBarOrLineGraph
      ? {
        xAxes: [{
          gridLines: {
            display: chartType !== "bar"
          },
          ticks: {
            beginAtZero: chartType === "horizontalBar",
            callback: value => chartType === "horizontalBar"
              ? numberFormatter(value)
              : value
          }
        }],
        yAxes: [{
          gridLines: {
            display: chartType !== "horizontalBar"
          },
          ticks: {
            beginAtZero: chartType !== "horizontalBar",
            callback: value => chartType !== "horizontalBar"
              ? numberFormatter(value)
              : value
          }
        }]
      }
      : {},
    tooltips: {
      enabled: true,
      mode: "index",
      callbacks: {
        title: (tooltipItem, data) => data.labels[tooltipItem[0].index] || '',
        label: (tooltipItem, data) => {
          const dataset = data.datasets[tooltipItem.datasetIndex];
          return `${dataset.label}: ${numberFormatter(dataset.data[tooltipItem.index] || '')}`
        }
      }
    }
  }
};

const getData = (chartType, jsonStat, layout, labelFormat) => {

  const {
    primaryDim,
    primaryDimValues,
    secondaryDim,
    secondaryDimValues,
    filtersValue
  } = layout;

  const indexesMap = getDimensionValuesIndexesMap(jsonStat);

  const valorizedDataMap = {};
  secondaryDimValues.forEach(secondaryDimValue => {
    valorizedDataMap[secondaryDimValue] = new Array(primaryDimValues.length).fill(false);
  });

  const datasets = [];
  if (secondaryDimValues && secondaryDimValues.length > 0) {
    secondaryDimValues.forEach((secondaryDimValue, idx) => {
      const data = [];
      primaryDimValues.forEach(primaryDimValue => {
        const dimValueArray = jsonStat.id.map(dim => {
          if (dim === primaryDim[0]) {
            return primaryDimValue;
          } else if (dim === secondaryDim[0]) {
            return secondaryDimValue;
          } else {
            return filtersValue[dim];
          }
        });
        const valueIdx = getDataIdxFromDimensionsArray(
          dimValueArray.map((value, idx) => indexesMap[jsonStat.id[idx]][value]),
          jsonStat.size
        );
        const value = jsonStat.value[valueIdx];
        if (value) {
          data.push(value);
          valorizedDataMap[secondaryDimValue][idx] = true;
        } else {
          data.push(null);
        }
      });
      if (valorizedDataMap[secondaryDimValue].find(el => el === true)) {
        datasets.push({
          label: getDimensionValueLabelFromJsonStat(jsonStat, secondaryDim[0], secondaryDimValue, labelFormat),
          data: data
        });
      }
    });

  } else {
    const data = [];
    primaryDimValues.forEach(primaryDimValue => {
      const dimValueArray = jsonStat.id.map(dim => {
        if (dim === primaryDim[0]) {
          return primaryDimValue;
        } else {
          return filtersValue[dim];
        }
      });
      const valueIdx = getDataIdxFromDimensionsArray(
        dimValueArray.map((value, idx) => indexesMap[jsonStat.id[idx]][value]),
        jsonStat.size
      );
      const value = jsonStat.value[valueIdx];
      if (value) {
        data.push(value);
      } else {
        data.push(null);
      }
    });
    datasets.push({
      label: "",
      data: data
    });
  }

  // TODO: controllare se almeno un dataset  valorizzato per uno specifico primaryDimValue (posizione x di ogni valorizedDataMap[secondaryDimValue])

  const isBarOrLineGraph =
    chartType === "bar" ||
    chartType === "horizontalBar" ||
    chartType === "line" ||
    chartType === "area" ||
    chartType === "radar";

  let colorArr = colors;
  if (datasets.length > 0) {
    while (colorArr.length < datasets[0].data.length) {
      colorArr = colorArr.concat(colorArr);
    }
    if (colorArr.length > datasets[0].data.length) {
      colorArr = colorArr.slice(0, datasets[0].data.length)
    }
  }

  return {
    labels: primaryDimValues.map(value => getDimensionValueLabelFromJsonStat(jsonStat, primaryDim[0], value, labelFormat)),
    datasets: datasets.map((ds, idx) => ({
      ...ds,
      borderWidth: 1,
      lineTension: chartType === "area" ? 0 : undefined,
      fill: (chartType === "area" || chartType === "radar"),
      backgroundColor: (isBarOrLineGraph && colorArr.length > 0)
        ? hexToRGBA(colors[idx % colors.length], 0.3)
        : colorArr.map(col => hexToRGBA(col, 0.3)),
      borderColor: (isBarOrLineGraph && colorArr.length > 0)
        ? colors[idx % colors.length]
        : colorArr
    }))
  };
};

export default class Chart extends Component {

  constructor(props) {
    super(props);
    this.chartRef = createRef();
  }

  componentDidMount() {

    const {
      jsonStat,
      type,
      layout,
      labelFormat
    } = this.props;

    ChartJs.defaults.global.defaultFontFamily = "'Roboto', 'Helvetica', 'Arial', sans-serif";
    this.myChart = new ChartJs(this.chartRef.current, {
      type: type === "area" ? "line" : type,
      options: getOptions(type),
      data: getData(type, jsonStat, layout, labelFormat)
    });
  }

  componentDidUpdate(prevProps) {

    const {
      jsonStat,
      type,
      layout,
      labelFormat
    } = this.props;

    if (prevProps.jsonStat !== jsonStat || prevProps.type !== type || prevProps.layout !== layout) {

      this.myChart.config.type = type === "area" ? "line" : type;
      this.myChart.options = getOptions(type);
      this.myChart.data = getData(type, jsonStat, layout, labelFormat);
      this.myChart.update();
    }
  }

  render() {
    return <canvas ref={this.chartRef}/>;
  }
}