import React from "react";
import Tooltip from "@material-ui/core/Tooltip";
import InfoIcon from '@material-ui/icons/Info';
import IconButton from "@material-ui/core/IconButton";

export const LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME = "LABEL_FORMAT_SELECTOR_FORMAT_NAME";
export const LABEL_FORMAT_SELECTOR_LABEL_FORMAT_ID = "LABEL_FORMAT_SELECTOR_FORMAT_ID";
export const LABEL_FORMAT_SELECTOR_LABEL_FORMAT_BOTH = "LABEL_FORMAT_SELECTOR_FORMAT_BOTH";

const InfoTooltip = ({children}) => {

  return children
    ? (
      <Tooltip title={children} placement="top">
        <IconButton
          aria-label="info"
          style={{
            color: "#00295A",
            padding: 0,
            transform: "translateY(-1px)"
          }}
        >
          <InfoIcon fontSize="small"/>
        </IconButton>
      </Tooltip>
    )
    : (
      <IconButton
        aria-label="info"
        style={{
          color: "#00295A",
          padding: 0,
          transform: "translateY(-1px)"
        }}
      >
        <InfoIcon fontSize="small"/>
      </IconButton>
    );
};

export default InfoTooltip;