import React from 'react';
import {ThemeProvider} from "@material-ui/core/styles";
import {createMuiTheme, CssBaseline} from "@material-ui/core";
import NotImplementedSnackbar from "./components/not-implemented-snackbar/NotImplementedSnackbar";
import Spinner from "./components/spinner/Spinner";
import ErrorSnackbar from "./components/error-snackbar/ErrorSnackbar";
import HubDomain from "./domains/HubDomain";
import Helmet from "react-helmet";
import {connect} from "react-redux";
import DeleteConflictModal from "./components/delete-conflict-modal";

const theme = createMuiTheme({
  palette: {
    text: {
      primary: "#00295a"
    },
    primary: {
      main: "#00295a",
      light: "#3b5187",
      dark: "#000030",
      contrastText: "#ffffff"
    },
    secondary: {
      main: "#f8a81e",
      light: "#ffd956",
      dark: "#c07900",
      contrastText: "#00295a"
    }
  }
});

const App = ({language}) =>
  <ThemeProvider theme={theme}>
    <Helmet>
      <html lang={language}/>
    </Helmet>
    <CssBaseline/>
    <Spinner>
      <NotImplementedSnackbar/>
      <ErrorSnackbar/>
      <DeleteConflictModal/>
      <HubDomain/>
    </Spinner>
  </ThemeProvider>;

export default connect(state => ({
  language: state.app.language
}))(App);