import {Reducer} from "redux";
import {APP_LANGUAGE_SET} from "./appActions";
import {INIT} from '../rootActions';
import {PERSISTENCE_ACTION_KEY} from '../../middlewares/persistence/middleware';
import _ from "lodash";

export type AppState = {
    language: string,
    languages: string[]
} | null
    ;
const appReducer: Reducer<AppState> = (
    state = null,
    action
) => {
    switch (action.type) {
        case INIT:
            const initialState = {
                language: action.payload.defaultLanguage,
                languages: action.payload.supportedLanguages
            };
            return _.merge(initialState, action.payload[PERSISTENCE_ACTION_KEY].app) || initialState;
        case APP_LANGUAGE_SET: {
            return {
                ...state,
                language: action.payload.language
            }
        }
        default:
            return state;
    }
};

export default appReducer;