import {APP_LANGUAGE_SET} from "../../state/app/appActions";
import i18n from 'i18next';
import {INIT} from "../../state/rootActions";
import {PERSISTENCE_ACTION_KEY} from "../persistence/middleware";

const i18nMiddleware = ({getState}) => next => action => {

  const res = next(action);

  if (action.type === APP_LANGUAGE_SET) {
    i18n.changeLanguage(action.payload.language, () => {
      window.location.reload();
    });
  }

  if (
    action.type === INIT &&
    action.payload[PERSISTENCE_ACTION_KEY]?.app?.language
  ) {
    if (getState().app.languages.includes(action.payload[PERSISTENCE_ACTION_KEY]?.app?.language)) {
      i18n.changeLanguage(action.payload[PERSISTENCE_ACTION_KEY].app.language)
    } else {

    }
  }

  return res;

};

export default i18nMiddleware;