import {Reducer} from "@reduxjs/toolkit";
import {REQUEST_ERROR, REQUEST_INIT, REQUEST_SUCCESS} from "../../middlewares/request/requestActions";
import {
  ALL_FULL,
  ALL_PARTIAL,
  CRITERIA_SELECTION_MODE_ALL,
  CRITERIA_SELECTION_MODE_STEP_BY_STEP,
  CRITERIA_SELECTION_TYPE_DYNAMIC,
  CRITERIA_SELECTION_TYPE_FULL,
  CRITERIA_SELECTION_TYPE_PARTIAL,
  DATASET_ACCESSIBLE_FETCH,
  DATASET_CRITERIA_ALERT_HIDE,
  DATASET_CRITERIA_HIDE,
  DATASET_CRITERIA_LENGTH_WARNING_HIDE,
  DATASET_CRITERIA_LENGTH_WARNING_SHOW,
  DATASET_CRITERIA_SHOW,
  DATASET_DOWNLOAD_SUBMIT,
  DATASET_DOWNLOAD_WARNING_HIDE,
  DATASET_FETCH,
  DATASET_FETCH_ENABLE,
  DATASET_HTML_GENERATING_TIME_SET,
  DATASET_LABEL_FORMAT_SET,
  DATASET_RESET,
  DATASET_STRUCTURE_CODELIST_FETCH,
  DATASET_STRUCTURE_CODELIST_FILTER_SET,
  DATASET_STRUCTURE_CODELISTS_FETCH,
  DATASET_STRUCTURE_FETCH,
  DATASET_STRUCTURE_FETCH_ENABLE,
  DATASET_TERRITORY_FETCH,
  DATASET_UNAVAILABLE_VIEW_WARNING_HIDE,
  DATASET_VIEWER_SET,
  STEP_BY_STEP_FULL,
  STEP_BY_STEP_PARTIAL
} from "./datasetActions";
import {getTreeFromArray} from "../../utils/tree";
// @ts-ignore
import _ from "lodash"
import moment from "moment";
import {getFilteredLayoutObject, getInitialFiltersValue, getJsonStatTableSize} from "../../utils/jsonStat";
import {LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME} from "../../components/label-format-selector";
import {getViewerIdxFromType} from "../../components/data-viewer";
import {localizeI18nObj} from "../../utils/i18n";

const TIME_PERIOD_KEY = "TIME_PERIOD";
const FREQ_KEY = "FREQ";

export const GENERATING_HTML_TIME_KEY = "generatingHtml";
export const OBSERVATION_COUNT_KEY = "observationCount";
export const SERVER_TIMINGS_KEY = "serverTimings";

const MAX_ALLOWED_CELLS = 1000000;

const getCompleteTableLayout = (initialLayout: any, jsonStat: any) => {
  const layout = _.cloneDeep(initialLayout);

  layout.rows = (initialLayout.rows || []).filter((dim: string) => jsonStat.id.includes(dim));
  layout.cols = (initialLayout.cols || []).filter((dim: string) => jsonStat.id.includes(dim));
  layout.sections = (initialLayout.sections || []).filter((dim: string) => jsonStat.id.includes(dim));

  layout.filters = [];
  jsonStat.id.forEach((dim: any) => {
    if (!layout.rows.includes(dim) && !layout.cols.includes(dim) && !layout.sections.includes(dim)) {
      layout.filters.push(dim);
    }
  })

  layout.filtersValue = getInitialFiltersValue(jsonStat, layout.filters);

  return layout
};

const getMinAndMax = (criteria: any) => {
  let min = moment().add(-20, 'y').format("YYYY") + "-01-01";
  let max = moment().add(10, 'y').format("YYYY") + "-12-31";

  return {
    min: criteria.values && criteria.values[0] && criteria.values[0].id
      ? moment(criteria.values[0].id).format("YYYY-MM-DD")
      : min,
    max: criteria.values && criteria.values[1] && criteria.values[1].id
      ? moment(criteria.values[1].id).format("YYYY-MM-DD")
      : max
  }
};

export const FREQ_ANNUAL = "A";
export const FREQ_SEMESTER = "S";
export const FREQ_QUARTERLY = "Q";
export const FREQ_MONTHLY = "M";
const frequencies = [FREQ_MONTHLY, FREQ_QUARTERLY, FREQ_SEMESTER, FREQ_ANNUAL];

const getFreq = (values: any) => {
  return (frequencies.find(freq => (values || []).includes(freq)) || null)
};

type DatasetState = {
  dataset: any | null,
  datasetId: string | null,
  isPartialData: boolean,
  isEmptyData: boolean,
  isTooBigData: boolean,
  isCriteriaVisible: boolean,
  isCriteriaAlertVisible: boolean,
  dimensions: any[] | null,
  mode: string | null,
  type: string | null,
  viewerIdx: number,
  hasViewTemplate: boolean,
  hasTemplateLayout: boolean,
  hasAnnotationLayout: boolean,
  initialTableLayout: any | null,
  initialChartLayout: any | null,
  initialMapLayout: any | null,
  labelFormat: string | null,
  filters: any | null,
  initialFilters: any | null,
  decimalSeparator: string | null,
  decimalPlaces: number | null
  emptyChar: string | null,
  enableCriteria: boolean,
  enableLayout: boolean,
  maxAllowedCells: number,
  territorialDims: string[] | null,
  codes: any[] | null,
  isCodesFlat: boolean,
  codelists: any[] | null,
  areCodelistsFlat: boolean,
  codelistsLength: number[] | null,
  timePeriod: {
    minDate: string | null,
    maxDate: string | null,
    fromDate: string | null,
    toDate: string | null,
    freq: string | null
  },
  timings: any | null,
  isFetchStructureDisabled: boolean,
  isFetchDatasetDisabled: boolean,
  isDownloadWarningVisible: boolean,
  isUnavailableViewWarningVisible: boolean,
  isCriteriaLengthWarningVisible: boolean,
  geometries: any | null,
  geometryDetailLevels: any | null
};

const initialState = {
  dataset: null,
  datasetId: null,
  isPartialData: false,
  isEmptyData: false,
  isTooBigData: false,
  isCriteriaVisible: false,
  isCriteriaAlertVisible: false,
  dimensions: null,
  mode: null,
  type: null,
  viewerIdx: 0,
  hasViewTemplate: false,
  hasTemplateLayout: false,
  hasAnnotationLayout: false,
  initialTableLayout: null,
  initialChartLayout: null,
  initialMapLayout: null,
  labelFormat: null,
  filters: null,
  initialFilters: null,
  decimalSeparator: null,
  decimalPlaces: null,
  emptyChar: null,
  enableCriteria: true,
  enableLayout: true,
  maxAllowedCells: MAX_ALLOWED_CELLS,
  territorialDims: null,
  codes: null,
  isCodesFlat: false,
  codelists: null,
  areCodelistsFlat: false,
  codelistsLength: null,
  timePeriod: {
    minDate: null,
    maxDate: null,
    fromDate: null,
    toDate: null,
    freq: null
  },
  timings: null,
  isFetchStructureDisabled: true,
  isFetchDatasetDisabled: true,
  isDownloadWarningVisible: false,
  isUnavailableViewWarningVisible: false,
  isCriteriaLengthWarningVisible: false,
  geometries: null,
  geometryDetailLevels: null
};

const datasetReducer: Reducer<DatasetState> = (
  state = initialState,
  action
) => {
  switch (action.type) {
    case DATASET_STRUCTURE_FETCH_ENABLE: {
      return {
        ...state,
        datasetId: action.datasetId,
        isFetchStructureDisabled: false
      }
    }
    case DATASET_FETCH_ENABLE: {
      return {
        ...state,
        isFetchDatasetDisabled: false
      }
    }
    case DATASET_CRITERIA_SHOW: {
      return {
        ...state,
        isCriteriaVisible: true
      }
    }
    case DATASET_CRITERIA_HIDE: {
      return {
        ...state,
        isCriteriaVisible: false,
        filters: state.initialFilters,
        codes: null,
        isCodesFlat: false,
        codelists: null,
        areCodelistsFlat: false,
        timePeriod: {
          ...state.timePeriod,
          minDate: null,
          maxDate: null,
          fromDate: null,
          toDate: null
        }
      }
    }
    case DATASET_CRITERIA_ALERT_HIDE: {
      return {
        ...state,
        isCriteriaAlertVisible: false
      }
    }
    case DATASET_STRUCTURE_CODELIST_FILTER_SET: {

      let fromDate = state.timePeriod.fromDate;
      let toDate = state.timePeriod.toDate;
      let freq = state.timePeriod.freq;

      if (action.filters[TIME_PERIOD_KEY]) {
        fromDate = action.filters[TIME_PERIOD_KEY][0];
        toDate = action.filters[TIME_PERIOD_KEY][1];
      }

      if (action.filters[FREQ_KEY]) {
        freq = getFreq(action.filters[FREQ_KEY])
      }

      return {
        ...state,
        filters: action.filters,
        timePeriod: {
          ...state.timePeriod,
          fromDate: fromDate,
          toDate: toDate,
          freq: freq
        }
      }
    }
    case DATASET_RESET: {
      return initialState
    }
    case DATASET_VIEWER_SET: {
      return {
        ...state,
        viewerIdx: action.viewerIdx
      }
    }
    case DATASET_LABEL_FORMAT_SET: {
      return {
        ...state,
        labelFormat: action.labelFormat
      }
    }
    case DATASET_DOWNLOAD_WARNING_HIDE: {
      return {
        ...state,
        isDownloadWarningVisible: false
      }
    }
    case DATASET_UNAVAILABLE_VIEW_WARNING_HIDE: {
      return {
        ...state,
        isUnavailableViewWarningVisible: false
      }
    }
    case DATASET_CRITERIA_LENGTH_WARNING_SHOW: {
      return {
        ...state,
        isCriteriaLengthWarningVisible: true,
        isFetchDatasetDisabled: true
      }
    }
    case DATASET_CRITERIA_LENGTH_WARNING_HIDE: {
      return {
        ...state,
        isCriteriaLengthWarningVisible: false
      }
    }
    case DATASET_HTML_GENERATING_TIME_SET: {
      return {
        ...state,
        timings: {
          ...state.timings,
          [GENERATING_HTML_TIME_KEY]: action.time
        }
      }
    }
    case REQUEST_INIT: {
      switch (action.payload.label) {
        case DATASET_STRUCTURE_FETCH: {
          return {
            ...state,
            isFetchStructureDisabled: true
          }
        }
        case DATASET_FETCH: {
          return {
            ...state,
            dataset: null,
            isFetchDatasetDisabled: true
          }
        }
        default:
          return state
      }
    }
    case REQUEST_SUCCESS: {
      switch (action.payload.label) {
        case DATASET_STRUCTURE_FETCH: {
          const criteriaView = action.payload.response.criteriaView;

          const VIEW_KEY = "view";
          const TEMPLATE_KEY = "template";

          const viewTemplate = action.payload.response[VIEW_KEY]
            ? action.payload.response[VIEW_KEY]
            : action.payload.response[TEMPLATE_KEY]
              ? action.payload.response[TEMPLATE_KEY]
              : null

          const viewTemplateLayouts = viewTemplate
            ? JSON.parse(viewTemplate.layouts)
            : null;

          const tableLayout = viewTemplateLayouts?.tableLayout
            ? viewTemplateLayouts.tableLayout
            : action.payload.response.layout
              ? action.payload.response.layout
              : null;

          const mapLayout = viewTemplateLayouts?.mapLayout
            ? viewTemplateLayouts.mapLayout
            : null;

          const chartLayout = viewTemplateLayouts?.chartLayout
            ? viewTemplateLayouts.chartLayout
            : null;

          let filters = viewTemplate
            ? viewTemplate.criteria
            : (action.payload.response.filters && action.payload.response.filters.filterData)
              ? action.payload.response.filters.filterData
              : null;

          if (filters) {
            const dimensionsId = action.payload.response.criteria.map(({id}: any) => id);
            Object.keys(filters).forEach(key => {
              if (!dimensionsId.includes(key)) {
                filters[key] = undefined;
              }
            });
          }

          const hasViewTemplate = (viewTemplate !== null && viewTemplate !== undefined);

          return {
            ...state,
            hasViewTemplate: hasViewTemplate,
            hasTemplateLayout: !!action.payload.response[TEMPLATE_KEY],
            hasAnnotationLayout: !!action.payload.response.layout,
            dimensions: action.payload.response.criteria,
            mode: (criteriaView === ALL_FULL || criteriaView === ALL_PARTIAL)
              ? CRITERIA_SELECTION_MODE_ALL
              : CRITERIA_SELECTION_MODE_STEP_BY_STEP,
            type: (criteriaView === ALL_FULL || criteriaView === STEP_BY_STEP_FULL)
              ? CRITERIA_SELECTION_TYPE_FULL
              : (criteriaView === ALL_PARTIAL || criteriaView === STEP_BY_STEP_PARTIAL)
                ? CRITERIA_SELECTION_TYPE_PARTIAL
                : CRITERIA_SELECTION_TYPE_DYNAMIC,
            codelistsLength: state.codelistsLength ? state.codelistsLength : action.payload.response.criteria.map(() => null),
            isCriteriaVisible: (tableLayout === null && filters === null),
            viewerIdx: viewTemplate ? getViewerIdxFromType(viewTemplate.defaultView) : 0,
            isUnavailableViewWarningVisible: (!!action.payload.extra?.viewId && !hasViewTemplate),
            initialTableLayout: tableLayout,
            initialMapLayout: mapLayout,
            initialChartLayout: chartLayout,
            labelFormat: (viewTemplate?.layouts?.labelFormat || LABEL_FORMAT_SELECTOR_LABEL_FORMAT_NAME),
            filters: (filters || {}),
            initialFilters: (filters || {}),
            decimalSeparator: (viewTemplate && viewTemplate.decimalSeparator)
              ? localizeI18nObj(viewTemplate.decimalSeparator, action.payload.extra.defaultlanguage, action.payload.extra.languages)
              : action.payload.response.decimalSeparator,
            decimalPlaces: (viewTemplate?.decimalNumber || action.payload.response.decimalPlaces),
            emptyChar: (viewTemplateLayouts?.tableEmptyChar || action.payload.response.emptyCellPlaceHolder),
            territorialDims: (action.payload.response.territorialDimensions || []),
            enableCriteria: viewTemplate?.enableCriteria !== false,
            enableLayout: viewTemplate?.enableLayout !== false,
            isFetchDatasetDisabled: (tableLayout === null && filters === null),
            timePeriod: {
              ...state.timePeriod,
              freq: getFreq((filters || {})[FREQ_KEY])
            },
            maxAllowedCells: (action.payload.response.maxTableCells || MAX_ALLOWED_CELLS)
          }
        }
        case DATASET_FETCH: {

          const cellCount = getJsonStatTableSize(action.payload.response)

          const isResponseNotValid = (
            action.payload.extra.status === 204 ||
            cellCount === 0 || cellCount > state.maxAllowedCells ||
            action.payload.response.id.length === 0
          );

          return {
            ...state,
            dataset: (action.payload.response?.id || "").length !== 0 ? action.payload.response : null,

            isTooBigData: cellCount > state.maxAllowedCells,
            isEmptyData: (action.payload.extra.status === 204 || (action.payload.response?.id || "").length === 0),
            isPartialData: action.payload.extra.status === 206,

            isCriteriaVisible: isResponseNotValid,
            isCriteriaAlertVisible: isResponseNotValid,

            initialTableLayout: (action.payload.extra.tableLayout && (action.payload.response?.id || "").length !== 0)
              ? {
                ...action.payload.extra.tableLayout,
                filtersValue: getInitialFiltersValue(action.payload.response, action.payload.extra.tableLayout.filters)
              }
              : (state.initialTableLayout && state.hasViewTemplate)
                ? getFilteredLayoutObject(action.payload.response, state.initialTableLayout)
                : (state.initialTableLayout && !isResponseNotValid)
                  ? getCompleteTableLayout(state.initialTableLayout, action.payload.response)
                  : null,
            initialMapLayout: (state.initialMapLayout && !isResponseNotValid)
              ? getFilteredLayoutObject(action.payload.response, state.initialMapLayout)
              : null,
            initialChartLayout: (state.initialChartLayout && !isResponseNotValid)
              ? getFilteredLayoutObject(action.payload.response, state.initialChartLayout)
              : null,

            initialFilters: isResponseNotValid ? state.initialFilters : state.filters,

            codes: isResponseNotValid ? state.codes : null,
            isCodesFlat: isResponseNotValid ? state.isCodesFlat : false,
            codelists: isResponseNotValid ? state.codelists : null,
            areCodelistsFlat: isResponseNotValid ? state.areCodelistsFlat : false,
            timePeriod: isResponseNotValid
              ? state.timePeriod
              : {
                ...state.timePeriod,
                minDate: null,
                maxDate: null,
                fromDate: null,
                toDate: null
              },
            isCriteriaLengthWarningVisible: false,
            timings: {
              [OBSERVATION_COUNT_KEY]: action.payload.response?.value ? Object.keys(action.payload.response.value).length : null,
              [SERVER_TIMINGS_KEY]: action.payload.extra.responseHeaders.backendtimers
                ? JSON.parse(action.payload.extra.responseHeaders.backendtimers)
                : null
            }
          }
        }
        case DATASET_ACCESSIBLE_FETCH: {
          return {
            ...state
          }
        }
        case DATASET_STRUCTURE_CODELIST_FETCH: {

          const criteria = action.payload.response.criteria[0] || {};

          let flatCodes = criteria.values ? criteria.values : [];
          const codelistIdx = criteria.id ? (state.dimensions || []).map(({id}: any) => id).indexOf(criteria.id) : -1;

          const newCodelistsLength = state.codelistsLength ? [...state.codelistsLength] : [];
          if (codelistIdx >= 0 && criteria.id !== TIME_PERIOD_KEY) {
            newCodelistsLength[codelistIdx] = flatCodes.length;
          }

          let isCodesFlat = true;
          flatCodes.forEach((item: any) => {
            if (isCodesFlat && item.parentId !== null && item.parentId !== undefined) {
              isCodesFlat = false;
            }
          })

          let minAndMax = {
            min: null,
            max: null
          };
          if (criteria.id === TIME_PERIOD_KEY) {
            // @ts-ignore
            minAndMax = getMinAndMax(criteria);
          }

          let freq = state.timePeriod.freq;
          if (!freq && criteria.id === FREQ_KEY) {
            freq = getFreq((criteria.values || []).map(({id}: any) => id));
          }

          return {
            ...state,
            codes: getTreeFromArray((flatCodes || []), "parentId", "children"),
            isCodesFlat: isCodesFlat,
            timePeriod: {
              minDate: state.timePeriod.minDate || minAndMax.min,
              maxDate: state.timePeriod.maxDate || minAndMax.max,
              fromDate: (state.filters[TIME_PERIOD_KEY] && state.filters[TIME_PERIOD_KEY].length === 2)
                ? state.filters[TIME_PERIOD_KEY][0]
                : minAndMax.min,
              toDate: (state.filters[TIME_PERIOD_KEY] && state.filters[TIME_PERIOD_KEY].length === 2)
                ? state.filters[TIME_PERIOD_KEY][1]
                : minAndMax.max,
              freq: freq
            },
            codelistsLength: newCodelistsLength
          }
        }
        case DATASET_STRUCTURE_CODELISTS_FETCH: {

          const criteria = action.payload.response.criteria;

          const flatCodelists = criteria ? criteria.map(({values}: any) => (values || [])) : [];

          let areCodelistsFlat = flatCodelists.map(() => true);
          flatCodelists.forEach((codelist: any, idx: number) => {
            codelist.forEach((item: any) => {
              if (areCodelistsFlat[idx] && item.parentId !== null && item.parentId !== undefined) {
                areCodelistsFlat[idx] = false;
              }
            })
          })

          const timePeriodIdx = (state.dimensions || []).map(({id}: any) => id).indexOf(TIME_PERIOD_KEY);
          const minAndMax = getMinAndMax(criteria[timePeriodIdx]);

          const freqIdx = (state.dimensions || []).map(({id}: any) => id).indexOf(FREQ_KEY);

          return {
            ...state,
            codelists: flatCodelists.map((flatCodes: any) => getTreeFromArray((flatCodes || []), "parentId", "children")),
            areCodelistsFlat: areCodelistsFlat,
            timePeriod: {
              minDate: state.timePeriod.minDate || minAndMax.min,
              maxDate: state.timePeriod.maxDate || minAndMax.max,
              fromDate: state.timePeriod.fromDate || minAndMax.min,
              toDate: state.timePeriod.toDate || minAndMax.max,
              freq: state.timePeriod.freq
                ? state.timePeriod.freq
                : getFreq((criteria[freqIdx].values || []).map(({id}: any) => id))
            },
            codelistsLength: flatCodelists.map((codelist: any, idx: number) => idx !== timePeriodIdx ? codelist.length : null)
          }
        }
        case DATASET_TERRITORY_FETCH: {
          const detailLevels = action.payload.response.map((val: any, idx: number) => val.nutsLevel)
            .reduce((acc: any, val: any) => {
              if (!acc[val]) {
                const label = "Nuts level " + val;
                acc[val] = {level: val, counter: 0, label: label};
              }
              acc[val].counter++;
              return acc;
            }, [])
            .filter((v: any) => {
              if (v.counter <= 1) {
                console.log("hiding NUTS level " + v.level + " (size = " + v.counter + ")");
              }
              return v.counter > 1
            }).sort((a: any, b: any) => a.level - b.level);

          return {
            ...state,
            geometries: action.payload.response,
            geometryDetailLevels: detailLevels
          }
        }
        default:
          return state
      }
    }
    case REQUEST_ERROR: {
      switch (action.payload.label) {
        case DATASET_FETCH: {
          const isPayloadTooLarge = action.payload.statusCode === 413

          return {
            dataset: null,
            datasetId: null,
            isTooBigData: isPayloadTooLarge,
            isEmptyData: isPayloadTooLarge ? state.isEmptyData : false,
            isPartialData: isPayloadTooLarge ? state.isPartialData : false,
            isCriteriaVisible: isPayloadTooLarge,
            isCriteriaAlertVisible: isPayloadTooLarge,
            dimensions: isPayloadTooLarge ? state.dimensions : null,
            mode: isPayloadTooLarge ? state.mode : null,
            type: isPayloadTooLarge ? state.type : null,
            viewerIdx: isPayloadTooLarge ? state.viewerIdx : 0,
            hasViewTemplate: state.hasViewTemplate,
            hasTemplateLayout: state.hasTemplateLayout,
            hasAnnotationLayout: state.hasAnnotationLayout,
            initialTableLayout: null,
            initialChartLayout: null,
            initialMapLayout: null,
            labelFormat: null,
            filters: isPayloadTooLarge ? state.filters : null,
            initialFilters: isPayloadTooLarge ? state.initialFilters : null,
            decimalSeparator: isPayloadTooLarge ? state.decimalSeparator : null,
            decimalPlaces: isPayloadTooLarge ? state.decimalPlaces : null,
            emptyChar: isPayloadTooLarge ? state.emptyChar : null,
            enableCriteria: isPayloadTooLarge ? state.enableCriteria : true,
            enableLayout: isPayloadTooLarge ? state.enableLayout : true,
            maxAllowedCells: state.maxAllowedCells,
            territorialDims: isPayloadTooLarge ? state.territorialDims : null,
            codes: isPayloadTooLarge ? state.codes : null,
            isCodesFlat: isPayloadTooLarge ? state.isCodesFlat : false,
            codelists: isPayloadTooLarge ? state.codelists : null,
            areCodelistsFlat: isPayloadTooLarge ? state.areCodelistsFlat : false,
            codelistsLength: isPayloadTooLarge ? state.codelistsLength : null,
            timePeriod: isPayloadTooLarge
              ? state.timePeriod
              : {
                ...state.timePeriod,
                minDate: null,
                maxDate: null,
                fromDate: null,
                toDate: null
              },
            timings: null,
            isFetchStructureDisabled: true,
            isFetchDatasetDisabled: true,
            isDownloadWarningVisible: state.isDownloadWarningVisible,
            isUnavailableViewWarningVisible: state.isUnavailableViewWarningVisible,
            isCriteriaLengthWarningVisible: state.isCriteriaLengthWarningVisible,
            geometries: state.geometries,
            geometryDetailLevels: state.geometryDetailLevels
          }
        }
        case DATASET_DOWNLOAD_SUBMIT: {
          return {
            ...state,
            isDownloadWarningVisible: action.payload.statusCode === 406
          }
        }
        case DATASET_STRUCTURE_FETCH:
        case DATASET_STRUCTURE_CODELIST_FETCH:
        case DATASET_STRUCTURE_CODELISTS_FETCH: {
          return initialState
        }
        default:
          return state
      }
    }
    default:
      return state
  }
};

export default datasetReducer;