import React, {forwardRef, Fragment, useEffect, useImperativeHandle, useState} from 'react';
import FormControl from "@material-ui/core/FormControl";
import TextField from "@material-ui/core/TextField";
import {withStyles} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import {useForm} from 'react-hook-form'
import {compose} from "redux";
import {connect} from "react-redux";
import {
  clearUsersConfigUser,
  fetchUsersConfigUser,
  sendUsersConfigUserCreate,
  sendUsersConfigUserEdit,
} from "../../../../state/usersConfig/usersConfigActions";
import Alert from "@material-ui/lab/Alert";
import {useTranslation} from "react-i18next";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import {Visibility, VisibilityOff} from "@material-ui/icons";

const styles = theme => ({
  root: {
    height: 440
  },
  field: {
    marginTop: theme.spacing(3)
  },
  paper: {
    marginTop: theme.spacing(2),
    padding: theme.spacing(3)
  },
  tabContent: {
    overflowY: "auto",
    overflowX: "hidden",
    height: "calc(100% - 56px)",
    marginTop: 8,
    padding: "0 4px"
  },
  title: {
    fontSize: 16
  }
});


const Form = compose(withStyles(styles), forwardRef)(({classes, config, onSubmit, onCancel}, ref) => {

  const {register, errors, handleSubmit, watch} = useForm({
    defaultValues: {
      ...config
    }
  });

  const [showPassword, setShowPassword] = useState(false);
  const [showPasswordConfirm, setShowPasswordConfirm] = useState(false);

  const {t} = useTranslation();

  useImperativeHandle(ref, () => ({
    submit(f) {
      handleSubmit(val => {
        const data = {
          ...config,
          ...val
        };

        onSubmit(data);
        f(data);
      })();
    },
    cancel(f) {
      onCancel();
      f();
    }
  }));

  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <FormControl fullWidth>
          <TextField
            disabled={!!config}
            name="email"
            label={t("scenes.usersSettings.userSettings.fields.email")}
            inputRef={register({required: t("commons.validation.required")})}
            error={!!errors.email}
            helperText={errors.email?.message}
            variant="outlined"
            required
          />
        </FormControl>
      </Grid>
      {!config && (
        <Grid item xs={12}>
          <FormControl fullWidth>
            <TextField
              disabled={!!config}
              name="emailConfirm"
              label={t("scenes.usersSettings.userSettings.fields.emailConfirm")}
              inputRef={
                register({
                  required: t("commons.validation.required"),
                  validate: val =>
                    val === watch('email') ||
                    t("scenes.usersSettings.userSettings.validation.emailsMustBeEqual")
                })
              }
              error={!!errors.emailConfirm}
              helperText={errors.emailConfirm?.message}
              variant="outlined"
              required
            />
          </FormControl>
        </Grid>
      )}
      <Grid item xs={12}>
        <FormControl fullWidth>
          <TextField
            name="firstName"
            label={t("scenes.usersSettings.userSettings.fields.firstName")}
            error={!!errors.name}
            helperText={errors.name?.message}
            variant="outlined"
            required
            inputRef={register({required: t("commons.validation.required")})}
          />
        </FormControl>
      </Grid>
      <Grid item xs={12}>
        <FormControl fullWidth>
          <TextField
            name="lastName"
            label={t("scenes.usersSettings.userSettings.fields.lastName")}
            inputRef={register({required: t("commons.validation.required")})}
            error={!!errors.surname}
            helperText={errors.surname?.message}
            variant="outlined"
            required
          />
        </FormControl>
      </Grid>
      <Grid item xs={12}>
        <FormControl fullWidth>
          <TextField
            name="organization"
            label={t("scenes.usersSettings.userSettings.fields.organization")}
            error={!!errors.organization}
            helperText={errors.organization?.message}
            variant="outlined"
            required
            inputRef={register({required: t("commons.validation.required")})}
          />
        </FormControl>
      </Grid>
      {!config && (
        <Fragment>
          <Grid item xs={12}>
            <FormControl fullWidth>
              <TextField
                name="password"
                label={t("scenes.usersSettings.userSettings.fields.password")}
                error={!!errors.password}
                helperText={errors.password?.message}
                variant="outlined"
                required
                type={showPassword ? undefined : "password"}
                inputRef={register({required: t("commons.validation.required")})}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={() => setShowPassword(!showPassword)}
                      >
                        {showPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
              />
            </FormControl>
          </Grid>
          <Grid item xs={12}>
            <FormControl fullWidth>
              <TextField
                name="passwordConfirm"
                label={t("scenes.usersSettings.userSettings.fields.passwordConfirm")}
                error={!!errors.passwordConfirm}
                helperText={errors.passwordConfirm?.message}
                variant="outlined"
                required
                type={showPasswordConfirm ? undefined : "password"}
                inputRef={
                  register({
                    required: t("commons.validation.required"),
                    validate: val =>
                      val === watch('password') ||
                      t("scenes.usersSettings.userSettings.validation.passwordsMustBeEqual")
                  })
                }
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={() => setShowPasswordConfirm(!showPasswordConfirm)}
                      >
                        {showPasswordConfirm ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    </InputAdornment>
                  )
                }}
              />
            </FormControl>
          </Grid>
        </Fragment>
      )}
    </Grid>
  );
});

const mapStateToProps = state => ({
  config: state.usersConfig.user,
  userErrors: state.usersConfig.userErrors
});

const mapDispatchToProps = dispatch => ({
  fetchConfig: nodeId => dispatch(fetchUsersConfigUser(nodeId)),
  sendConfigCreate: (config, isAnonymous) => dispatch(sendUsersConfigUserCreate(config, isAnonymous)),
  sendConfig: config => dispatch(sendUsersConfigUserEdit(config)),
  clearConfig: () => dispatch(clearUsersConfigUser())
});

const UserSettingsForm = ({config, userId, fetchConfig, sendConfigCreate, sendConfig, clearConfig, userErrors, isAnonymous}, ref) => {

  const [needConfig, setNeedConfig] = useState(userId >= 0);

  useEffect(() => {

    if (needConfig) {
      setNeedConfig(false);
      fetchConfig(userId);
    }
  }, [config, needConfig, setNeedConfig, fetchConfig, userId]);

  return (userId === -1 || config) && (
    <Fragment>
      {userErrors !== null && (
        userErrors.map(str => <Alert severity="error" style={{marginBottom: 8}}>{str}</Alert>))}
      <Form
        config={userId === -1 ? undefined : config}
        ref={ref}
        onSubmit={
          userId === -1
            ? (config) => sendConfigCreate(config, isAnonymous)
            : sendConfig
        }
        onCancel={clearConfig}
      />
    </Fragment>

  );
};

export default compose(connect(mapStateToProps, mapDispatchToProps, null, {forwardRef: true}), forwardRef)(UserSettingsForm);
